/* breadcrumbs */
.breadcrumbs {
    padding: 20px 0 60px;
    font-family: var(--font-family);
    position: relative;
    z-index: 3;
}

.breadcrumbs,
.breadcrumbs li {
    align-items: center;
    display: flex;
    gap: 6px;
    flex-wrap: wrap;
}

.breadcrumbs li {
    font-size: 14px;
    color: var(--cveta-zelenyy);
}

.breadcrumbs li a {
    font-size: 14px;
    color: var(--cveta-zelenyy);
    transition: all 0.3s ease;
    &:hover {
        opacity: 0.7;
        font-weight: 300;
    }
}

.breadcrumbs li:after {
    content: "—";
    display: block;
    font-size: 20px;
    color: var(--cveta-zelenyy);
    font-size: inherit;
}

.breadcrumbs li:first-child {
    position: relative;
    padding-left: 24px;
}

.breadcrumbs li:first-child::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 16px;
    height: 16px;
    background: url("./../img/breadcrumbs/home.svg") no-repeat center center / contain;
}

.breadcrumbs li:last-child {
    opacity: 0.6;
}

.breadcrumbs li:last-child::after {
    content: none;
}

.breadcrumbs-white {
    padding: 0 0 40px;
    & li {
        color: var(--tekst-belyy);
        & a, &:after {
            color: var(--tekst-belyy);
        }
        &:first-child::before {
            background: url("./../img/breadcrumbs/home-white.svg") no-repeat center center / contain;
        }
    }
}

.page-top__title-3 {
    margin: 20px 0 40px 0;
}

.page-top__descr-3 {
    margin-bottom: 40px;
}

@media (max-width: 688px) {
    .breadcrumbs {
        padding: 20px 0 40px;
        & li, & li a {
            font-size: 12px;
        }
    }
    .breadcrumbs-white {
        padding: 0 0 40px;
    }

    .page-top__title-3 {
        margin: 5px 0 40px 0;
    }
}
