// doctors-2
.doctors {
    &__swiper-pagination {
        display: none;
    }

    &__swiper-slide {
        height: auto;
    }

    &__top {
        &_btns {
            display: flex;
            align-items: center;
            gap: 40px;
        }
    }

    &__swiper-btns {
        padding-right: 40px;
        border-right: 1px solid rgba(15,86,88,0.2);
    }

    &__card {
        display: flex;
        flex-direction: column;
        border: 1px solid rgba(111, 111, 111, 0.2);
        border-radius: 30px;
        overflow: hidden;
        height: 100%;

        &_inner {
            display: block;
            padding: 24px 28px 28px;
        }

        &_top {
            position: relative;
            overflow: hidden;
        }

        &_photo img {
            display: block;
            border-radius: 30px;
            width: 100%;
            height: 100%;
            max-height: 261px;
            object-fit: cover;
        }

        &_label {
            border-radius: 30px;
            padding: 10px 16px;
            backdrop-filter: blur(8.100000381469727px);
            background: rgba(7, 57, 58, 0.6);
            font-weight: 500;
            font-size: 14px;
            text-align: center;
            color: var(--cveta-salatovyy);
            position: absolute;
            bottom: 10px;
            left: 10px;
            right: 10px;
        }

        &_fio {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 16px;
        }

        &_items {
            display: flex;
            flex-direction: column;
            gap: 10px;
            margin-bottom: 24px;
        }

        &_item {
            display: flex;
            gap: 10px;
            font-size: 14px;
            color: var(--fony-plashek-seryy-2);
            & img {
                min-width: 18px;
            }
        }

        &_btns {
            display: flex;
            gap: 10px;
            & button, & a {
                flex-grow: 1;
                padding: 13px 16px;
            }
        }
    }
}


// media doctors-1
@media (max-width: 800px) {
    .doctors {
        &__top_btns {
            display: contents;
        }
        &__swiper-btns {
            display: none;
        }
        &__swiper-pagination {
            display: block;
        }
        &__cards {
            margin-bottom: 20px;
        }
    }
}
@media (max-width: 600px) {
    .doctors {
        &__card {
            &_inner {
                padding: 24px 20px 20px;
            }
            &_fio {
                font-size: 20px;
                margin-bottom: 16px;
            }
        }
    }
}
