// legal-info-1
.legal-info {
    &__title-h1 {
        margin-bottom: 50px;
    }

    &__block {
        display: flex;
        gap: 20px;
        margin-bottom: 60px;
    }

    &__accordion{
        display: contents;
    }

    &__title, &__text {
        padding-top: 20px;
        border-top: 1px solid #c7d6dc;
    }

    &__title {
        flex: 1 0 30%;
    }

    &__text {
        flex: 1 0 64%;
    }

    &__supervision {
        & p {
            &:not(:last-child) {
                margin-bottom: 8px;
            }
            &:first-child {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 18px;
                line-height: 133%;
                color: var(--cveta-zelenyy);
                margin-bottom: 16px;
            }
        }
        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }
}


// media legal-info-1
@media (max-width: 700px) {
    .legal-info {
        &__title-h1 {
            margin-bottom: 30px;
        }
        &__block {
            flex-direction: column;
            gap: 0;
            margin-bottom: 20px;
        }

        &__accordion {
            display: block;
            &.accordion--active {
                & .legal-info__title {
                    &::after {
                        transform: rotate(315deg);
                    }
                }
            }
        }
    
        &__title {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            color: var(--cveta-zelenyy);
            &::after {
                content: "";
                display: inline-block;
                min-width: 8px;
                width: 8px;
                height: 8px;
                border-top: 1px solid var(--cveta-zelenyy-2);
                border-right: 1px solid var(--cveta-zelenyy-2);
                transform: rotate(135deg);
                margin: 0;
                margin-top: 12px;
            }
        }
    
        &__text {
            padding-top: 0;
            border-top: 0;
            &.accordion__content--active {
                margin-bottom: 20px;
            }
        }
    
        &__supervision {
            & p:first-child {
                font-size: 14px;
                margin-bottom: 14px;
            }
        }
    }
}
