// stages-4
.stages {
    &__title {
        margin-bottom: 50px;
    }

    &__cards {
        & ol {
            list-style: none;
            counter-reset: item;
            & li {
                padding: 30px 20px;
                border-bottom: 1px solid rgba(84, 84, 84, 0.2);
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 20px;
                &:first-child {
                    border-top: 1px solid rgba(84, 84, 84, 0.2);
                }
                &::before {
                    content: "("counter(item, decimal-leading-zero)")" ;
                    counter-increment: item;
                    display: block;
                    font-family: var(--second-family);
                    font-weight: 400;
                    font-size: 34px;
                    line-height: 140%;
                    color: var(--cveta-salatovyy);
                    flex-basis: 10%;
                }
            }
        }
    }

    &__name {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 22px;
        line-height: 140%;
        color: var(--cveta-zelenyy);
        display: flex;
        align-items: center;
        gap: 40px;
        flex-basis: 25%;
    }

    &__text {
        flex-basis: 50%;
    }

    &__img img {
        display: block;
        border-radius: 200px;
        min-width: 82px;
        width: 82px;
        height: 82px;
        object-fit: cover;
    }
}


// media stages-4
@media (max-width: 900px) {
    .stages {
        &__cards ol li {
            flex-wrap: wrap;
        }
        &__name {
            order: -1;
            flex-basis: 70%;
        }
        &__text {
            flex-basis: 100%;
        }
    }
}
@media (max-width: 620px) {
    .stages {
        &__title {
            margin-bottom: 20px;
        }
        &__cards ol li {
            padding: 20px 0;
            &:first-child {
                border-top: none;
            }
            &::before {
                font-size: 22px;
            }
        }
        &__name {
            font-weight: 600;
            font-size: 18px;
            gap: 14px;
        }
        &__img img {
            min-width: 60px;
            width: 60px;
            height: 60px;
        }
    }
}