// footer-6
.footer {
    &__plashka {
        padding: 16px 0px;
        background: var(--cveta-salatovyy);
        font-weight: 500;
        font-size: 14px;
        line-height: 140%;
        text-align: center;
        color: var(--cveta-zelenyy);
    }

    &__inner {
        padding: 100px 0 50px;
        background: var(--cveta-zelenyy);
    }

    &__separator {
        padding-bottom: 24px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        margin-bottom: 24px;
    }

    &__logo {
        display: block;
        max-width: 128px;
        width: 100%;
        margin-bottom: 40px;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 30px;
        padding-bottom: 50px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        margin-bottom: 50px;

        &_left {
            flex-basis: 420px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 30px;
        }

        &_right {
            flex-basis: 762px;
            border: 1px solid var(--cveta-salatovyy);
            border-radius: 20px;
            padding: 60px;
            background: rgba(255, 255, 255, 0.1);
        }
    }

    &__btns {
        display: flex;
        gap: 10px;
    }

    &__btn-question {
        border: 1px solid var(--tekst-belyy);
        color: var(--tekst-belyy);
        background: transparent;
    }

    &__btn-eye {
        width: 61px;
        min-width: 61px;
        height: 61px;
        border-radius: 10px;
        background-color: rgba(255, 255, 255, 0.1);
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;

        &_item {
            display: flex;
            align-items: flex-start;
            gap: 10px;
        }

        &_icon {
            width: 40px;
            height: 40px;
            padding: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 40px;
            background: var(--tekst-belyy);
        }
        
        &_text {
            & > p:first-child {
                font-size: 14px;
                color: var(--fony-plashek-svetlo-seryy);
                opacity: 0.6;
                margin-bottom: 2px;
            }
            & > p:last-child, & a {
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                color: var(--tekst-belyy);
            }
            & button {
                text-decoration: underline;
            }
            & a:hover, & button:hover {
                color: var(--cveta-salatovyy);
            }
        }

        &_phone {
            & a {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 18px;
                color: var(--tekst-belyy);
            }
        }
    }

    &__pays {
        & p {
            font-weight: 400;
            color: rgba(255, 255, 255, 0.6);
            margin-bottom: 12px;
        }
        &_list {
            display: flex;
            gap: 10px;
            & img {
                display: block;
                width: 80px;
                height: 50px;
                padding: 15px 20px;
                border-radius: 10px;
                background-color: rgba(255, 255, 255, 0.05);
            }
        }
    }

    &__networks {
        & p {
            font-weight: 400;
            color: rgba(255, 255, 255, 0.6);
            margin-bottom: 12px;
        }
        &_list {
            display: flex;
            gap: 6px;
            & a {
                display: block;
                width: 40px;
                height: 40px;
                padding: 7px;
                border-radius: 5px;
                background-color: var(--tekst-belyy);
                border: 1px solid transparent;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all 0.4s ease;
                & svg path {
                    transition: all 0.4s ease;
                }
                &:hover {
                    border: 1px solid var(--tekst-belyy);
                    background-color: transparent;
                    & svg path {
                        fill: var(--tekst-belyy);
                    }
                }
            }
        }
    }

    &__bottom {
        display: flex;
        align-items: start;
        justify-content: space-between;
        gap: 20px;
        &_links {
            display: flex;
            flex-direction: column;
            gap: 10px;
            & a {
                text-decoration: underline;
                color: rgba(255, 255, 255, 0.6);
                &:hover {
                    opacity: 0.7;
                }
            }
        }
    }

    &__nav {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 40px;
        &_title {
            display: block;
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst-belyy);
            margin-bottom: 10px;
        }
        &_item {
            flex: 1 0 40%;
            & ul {
                display: flex;
                flex-direction: column;
                gap: 10px;
                & a {
                    color: var(--cveta-salatovyy);
                    transition: all 0.3s ease;
                    &:hover {
                        color: var(--belyy);
                        opacity: 0.9;
                    }
                }
            }
        }
    }

    &__nav-menu {
        padding-bottom: 30px;
        border-bottom: 1px solid var(--cveta-salatovyy);
        margin-bottom: 30px;
        & ul {
            display: flex;
            flex-wrap: wrap;
            gap: 14px 40px;
            & li {
                width: calc(100% / 5 - 32px);
            }
            & a {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 18px;
                line-height: 133%;
                color: var(--tekst-belyy);
                transition: all 0.3s ease;
                &:hover {
                    color: var(--cveta-salatovyy);
                }
            }
        }
    }

    &__copy {
        font-size: 14px;
        line-height: 140%;
        color: rgba(255, 255, 255, 0.5);
        & p:first-child {
            margin-bottom: 10px;
        }
    }
}


// media footer-6
@media (max-width: 1100px) {
    .footer {
        &__row {
            flex-direction: column;
            gap: 30px;
            &_left, &_right {
                flex-basis: 100%;
            }
        }
    }
}
@media (max-width: 800px) {
    .footer {
        &__plashka {
            font-size: 12px;
        }
        &__inner {
            padding: 40px 0;
        }
        &__logo {
            margin-bottom: 30px;
            position: relative;
            z-index: 2;
        }
        &__separator {
            padding-bottom: 30px;
            margin-bottom: 30px;
        }

        &__row {
            padding-bottom: 30px;
            margin-bottom: 30px;
            &_right {
                border-radius: 14px;
                padding: 20px;
            }
            &_left {
                display: contents;
            }
        }
        &__btns {
            & button {
                flex: 1 0 40%;
            }
        }
        &__btn-eye {
            width: 50px;
            min-width: 50px;
            height: 50px;
        }

        &__info {
            &_row {
                flex-direction: column;
                gap: 24px;
            }
            &_phone a {
                font-size: 18px;
            }
        }
        &__pays {
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 20px;
        }
        &__bottom {
            order: 3;
        }
        &__nav {
            gap: 20px;
            &_acc {
                display: flex;
                align-items: center;
                gap: 10px;
                transition: all 0.4s ease;
                &::before {
                    content: "";
                    display: inline-block;
                    width: 8px;
                    height: 8px;
                    border-top: 1.5px solid var(--belyy);
                    border-right: 1.5px solid var(--belyy);
                    transform: rotate(135deg);
                    margin-bottom: 4px;
                    transition: all 0.3s ease;
                }
                &.active {
                    margin-bottom: 12px;
                    &::before {
                        transform: rotate(315deg);
                    }
                }
            }
            &_title {
                font-size: 16px;
                margin-bottom: 0;
            }
            &_item {
                flex-basis: 100%;
                & ul {
                    max-height: 0;
                    overflow: hidden;
                    transition: all 0.3s ease;
                    &.active {
                        max-height: 500px;
                    }
                }
            }
        }
        &__nav-menu {
            & ul {
                display: flex;
                flex-wrap: wrap;
                gap: 20px;
                & li {
                    width: auto;
                    flex: 1 0 40%;
                }
                & a {
                    font-size: 16px;
                }
            }
        }
        &__copy {
            font-size: 12px;
        }
    }
}
@media (max-width: 600px) {
    .footer {
        &__btns {
            flex-direction: column;
        }
    }
}