// packages
.packages {
    &__title {
        margin-bottom: 50px;
    }

    &__swiper-slide {
        height: auto;
    }

    &__card {
        border: 1px solid rgba(111, 111, 111, 0.2);
        border-radius: 20px;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        height: 100%;

        &_top {
            border-bottom: 1px solid rgba(111, 111, 111, 0.2);
            padding: 24px 32px;
            background: var(--cveta-zelenyy);
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 120%;
            text-align: center;
            color: var(--tekst-belyy);
            display: flex;
            align-items: center;
            justify-content: center;
            min-height: 112px;
        }

        &_inner {
            padding: 20px 24px 24px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 46px;
            height: 100%;
        }

        &_list ul {
            display: flex;
            flex-direction: column;
            gap: 10px;
            & li {
                display: flex;
                gap: 10px;
                font-weight: 400;
                &::before {
                    content: "";
                    display: block;
                    width: 9px;
                    height: 9px;
                    border-radius: 100%;
                    background: var(--cveta-salatovyy);
                    margin-top: 7px;
                }
            }
        }

        &_price {
            border-radius: 25px;
            background: var(--cveta-salatovyy);
            & p {
                padding: 12px 0;
                font-weight: 600;
                font-size: 18px;
                line-height: 140%;
                text-align: center;
                color: var(--cveta-zelenyy);
                & span {
                    font-weight: 400;
                    font-size: 16px;
                    color: rgba(15, 86, 88, 0.4);
                    position: relative;
                    &:after {
                        content: "";
                        display: block;
                        width: 100%;
                        height: 1px;
                        background: var(--cveta-zelenyy);
                        position: absolute;
                        top: 50%;
                        left: 0;
                        transform: rotate(345deg);
                    }
                }
            }
        }

        &_btn {
            background: var(--cveta-zelenyy);
            color: #fff;
            width: 100%;
        }
    }
}

// media packages
@media (max-width: 688px) {
    .packages {
        overflow: hidden;
        &__swiper {
            overflow: visible;
        }
        &__title {
            margin-bottom: 30px;
        }
        &__cards {
            gap: 16px;
        }
        &__card {
            border-radius: 16px;
            &_top {
                padding: 20px;
                font-size: 22px;
                min-height: 100px;
            }
            &_inner {
                gap: 30px;
            }
            &_price {
                & p {
                    font-size: 16px;
                    & span {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}