// medicaments-1
.medicaments {
    &__title-h1 {
        margin-bottom: 50px;
    }

    &__grid {
        display: grid;
        grid-template-columns: 3.2fr 6.8fr;
        gap: 60px;
    }

    &__left {
        & p:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    &__inner {
        display: flex;
        flex-wrap: wrap;
        gap: 40px 20px;
    }

    &__block {
        width: calc(100% / 3 - 14px);
        & p {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 34px;
            line-height: 120%;
            color: var(--cveta-zelenyy);
            padding-bottom: 8px;
            border-bottom: 1px solid rgba(111, 111, 111, 0.2);
            margin-bottom: 20px;
        }
        & ul {
            display: flex;
            flex-direction: column;
            gap: 8px;
            & a {
                color: var(--fony-plashek-seryy-2);
                transition: all 0.3s ease;
                &:hover {
                    color: var(--cveta-zelenyy);
                    opacity: 0.7;
                }
            }
        }
    }
}

// media medicaments-1
@media (max-width: 1100px) {
    .medicaments {
        &__grid {
            grid-template-columns: 1fr;
            gap: 40px;
        }
    }
}
@media (max-width: 750px) {
    .medicaments {
        &__title-h1 {
            margin-bottom: 30px;
        }

        &__inner {
            gap: 24px 20px;
        }
    
        &__block {
            width: calc(50% - 10px);
            & p {
                font-size: 24px;
                margin-bottom: 16px;
            }
        }
    }
}
@media (max-width: 600px) {
    .medicaments {
        &__block {
            width: 100%;
        }
    }
}