// reviews-1
.reviews {
    padding: 100px 0 80px;
    background: var(--cveta-zelenyy);
    overflow: hidden;

    &__title {
        color: var(--cveta-salatovyy);
    }
    &__btn {
        color: var(--cveta-salatovyy);
        &::after {
            border-top: 1px solid var(--cveta-salatovyy);
            border-right: 1px solid var(--cveta-salatovyy);
        }
    }

    &__cards {
        display: flex;
        gap: 60px;
    }

    &__tab {
        &_btns {
            flex: 1 0 32%;
            display: flex;
            flex-direction: column;
            gap: 10px;
        }

        &_btn {
            border-radius: 20px;
            padding: 20px;
            background: rgba(255, 255, 255, 0.1);
            display: flex;
            align-items: center;
            gap: 16px;
            margin-right: 10px;
            width: 100%;
            color: var(--tekst-belyy);
            transition: all 0.3s ease;
            &:hover {
                background: rgba(255, 255, 255, 0.2);
            }
            &.tab__btn--active {
                background: var(--tekst-belyy);
                color: var(--cveta-zelenyy);
            }
        }

        &_contents {
            flex: 1 0 63%;
        }
    }

    &__photo {
        display: block;
        border-radius: 35px;
        min-width: 50px;
        width: 50px;
        height: 50px;
        overflow: hidden;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__fio {
        font-weight: 300;
        font-size: 14px;
        line-height: 140%;
        text-align: left;
        & p:first-child {
            font-weight: 500;
            font-size: 18px;
            margin-bottom: 4px;
        }
    }

    &__card {
        &_top {
            display: flex;
            justify-content: space-between;
            gap: 30px;
            padding-bottom: 20px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            margin-bottom: 20px;
        }

        &_info {
            display: flex;
            flex-direction: column;
            gap: 8px;
            color: var(--tekst-belyy);
            & span {
                font-weight: 500;
                color: var(--cveta-salatovyy);
            }
        }

        &_src {
            text-align: right;
            color: var(--tekst-belyy);
            opacity: 0.6;
        }
        &_publication{
            margin-bottom: 10px;
        }
        &_source {
            font-weight: 500;
        }

        &_main {
            color: var(--tekst-belyy);
            margin-bottom: 28px;
        }

        &_bottom {
            border: 1px solid var(--cveta-salatovyy);
            border-radius: 20px;
            padding: 20px;
            color: var(--tekst-belyy);
        }

        &_author {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            color: var(--cveta-salatovyy);
            margin-top: 20px;
            & p {
                font-weight: 500;
            }
            & span {
                font-size: 14px;
            }
        }
    }
}



// media reviews-1
@media (max-width: 1000px) {
    .reviews {
        &__cards {
            flex-direction: column;
            gap: 12px;
        }
        &__tab {
            &_btns {
                display: contents;
            }
            &_contents {
                display: contents;
            }
            &_content {
                padding: 10px 0;
            }
        }
    }
}
@media (max-width: 688px) {
    .reviews {
        padding: 50px 0;
        &__tab {
            &_btn {
                border-radius: 14px;
                padding: 14px;
            }
        }
        &__card {
            display: flex;
            flex-direction: column;
            &_top {
                display: contents;
            }
            &_info {
                order: 1;
                gap: 20px;
                padding-bottom: 20px;
                border-bottom: 1px solid rgba(255, 255, 255, 0.2);
                margin-bottom: 20px;
                & span {
                    display: block;
                }
            }
            &_src {
                order: 3;
                display: flex;
                justify-content: space-between;
                gap: 20px;
                text-align: left;
                margin-bottom: 20px;
            }
            &_publication{
                margin-bottom: 0;
                & span {
                    display: none;
                }
            }
            &_main {
                order: 2;
                margin-bottom: 20px;
            }
            &_bottom {
                order: 4;
                flex-direction: column;
                gap: 10px;
            }
            &_author {
                padding-top: 14px;
                margin-top: 14px;
                border-top: 1px solid rgba(255, 255, 255, 0.2);
                flex-direction: column;
                align-items: flex-start;
                gap: 8px;
            }
        }
    }
}
