/* linking */
.linking__title-2 {
    margin-bottom: 50px;
}

.linking__block-2:not(:last-child) {
    margin-bottom: 50px;
}

.linking__block_title-2 {
    margin-bottom: 40px;
}

.linking__inner-2 {
    display: flex;
    flex-wrap: wrap;
    gap: 60px;
    row-gap: 15px;


    &>* {
        flex-basis: calc(50% - 30px);
    }
}

.linking__item-2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    gap: 10px;
    color: var(--cveta-zelenyy);
    font-size: 16px;
    border-bottom: 1px solid rgba(107, 107, 107, 0.2);
}

.linking__item-name-2 {
    flex: 1 0 60%;
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 18px;
    line-height: 133%;
    color: var(--tekst-svetlyy);
}

.linking__item-bottom-2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    flex-basis: 30%;
}

.linking__btn-2 {
    max-width: 30px;
    max-height: 30px;
    min-width: 30px;
    min-height: 30px;
}

.all__linking-2 {
    max-height: 52px;
}

/* linking media */

@media (max-width: 1024px) {
    .linking__item-2 {
        font-size: 14px;
    }
}

@media (max-width: 930px) {
    .linking__item-2 {
        align-items: flex-start;
        flex-direction: column;
    }

    .linking__item-name-2 {
        flex: 1 0 auto;
    }

    .linking__item-bottom-2 {
        flex-basis: 100%;
        width: 100%;
    }
}

@media (max-width: 688px) {
    .linking__title-2 {
        margin-bottom: 40px;
    }
    .linking__block-2 {
        margin-bottom: 40px;
    }
    .linking__block_title-2 {
        margin-bottom: 20px;
    }
    .linking__item-2 {
        flex-basis: 100%;
    }
    .linking__inner-2 {
        gap: 10px;
    }
    .all__linking-2 {
        flex-basis: 100%;
    }
}