// site-map-1
.site-map {
    &__tile-h1 {
        margin-bottom: 50px;
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    &__accordion {
        display: contents;
    }
    &__item {
        border-radius: 20px;
        padding: 30px;
        background: var(--fony-plashek-svetlo-seryy);
        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 15px;
            padding-bottom: 15px;
            border-bottom: 1px solid #c7d6dc;
            margin-bottom: 15px;
        }

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst-temnyy);
        }


        &_list {
            &.accordion__content {
                display: contents;
            }
            & ul {
                display: flex;
                flex-direction: column;
                gap: 12px;
                & li a {
                    display: flex;
                    gap: 10px;
                    color: var(--tekst-svetlyy);
                    &:hover {
                        color: var(--cveta-zelenyy-2);
                    }
                    &::before {
                        content: "";
                        display: block;
                        border-radius: 10px;
                        min-width: 18px;
                        width: 18px;
                        height: 3px;
                        background: var(--cveta-zelenyy-2);
                        margin-top: 9px;
                    }
                }
            }
        }
    }

    &__items {
        border-radius: 20px;
        padding: 30px;
        background: var(--fony-plashek-svetlo-seryy);
        & ul{
            display: flex;
            flex-direction: column;
            gap: 30px;
            & a {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 22px;
                line-height: 140%;
                text-align: center;
                color: var(--tekst-temnyy);
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-bottom: 30px;
                border-bottom: 1px solid #c7d6dc;
                transition: all 0.3s ease;
                &:hover {
                    color: var(--cveta-zelenyy-2);
                }
            }
            & li:last-child a {
                padding-bottom: 0;
                border-bottom: none;
            }
        }
    }
}

// media site-map-1
@media (max-width: 1050px) {
    .site-map {
        &__grid {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 700px) {
    .site-map {
        &__tile-h1 {
            margin-bottom: 30px;
        }
        &__grid {
            grid-template-columns: repeat(1, 1fr);
            gap: 10px;
        }
        &__accordion {
            display: block;
            &.accordion--active {
                padding-bottom: 15px;
                border-bottom: 1px solid #c7d6dc;
                margin-bottom: 15px;
                & .site-map__brn-more::after {
                    transform: rotate(315deg);
                    margin: 0;
                }
            }
        }
        &__item {
            border-radius: 20px;
            padding: 18px 24px;
            &_top {
                padding-bottom: 0;
                border-bottom: none;
                margin-bottom: 0;
            }
            &_list {
                &.accordion__content {
                    display: block;
                }
            }
            &_title {
                font-size: 18px;
            }
            & .site-map__brn-more::after {
                transform: rotate(135deg);
                margin: 0;
                margin-bottom: 2px;
            }
        }
        &__items {
            border-radius: 20px;
            padding: 18px 24px;
            & ul {
                gap: 20px;
                a {
                    font-size: 16px;
                    padding-bottom: 20px;
                }
            }
        }
    }
}

