
// service-submenu-2
.service-submenu-2 {
    position: fixed;
    top: calc(100% + 10px);
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: calc(var(--container-width) + 40px);
    background-color: transparent;
    padding: 0;
    transition: all 0.5s ease;

    &__row {
        display: flex;
        gap: 10px;
        width: 100%; 
    }

    & .header {
        &__submenu {

            &_inner {
                box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
                flex: 1 0 70%;
                border-radius: 20px;
                padding: 30px;
                background: var(--tekst-belyy);
    
                & ul {
                    display: grid;
                    grid-template-columns: repeat(4, 1fr);
                    align-items: stretch;
                    gap: 10px;
                    max-height: 470px;
                    overflow-y: auto;
                    padding-right: 10px;
                    &::-webkit-scrollbar {
                        width: 4px;
                        background-color: var(--fony-plashek-seryy);
                    }
                    &::-webkit-scrollbar-thumb {
                        border-radius: 10px;
                        background-color: rgba(7, 57, 58, 0.3);
                    }
                    &::-webkit-scrollbar-track {
                        background-color: var(--fony-plashek-seryy);
                    }
    
                    & li {
                        min-width: auto;
                        max-width: 100%;
                        min-height: auto;
                    }
    
                    & a {
                        display: block;
                        border: 1px solid rgba(84, 84, 84, 0.2);
                        border-radius: 20px;
                        padding: 18px 20px;
                        font-family: var(--second-family);
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 140%;
                        color: var(--cveta-zelenyy);
                        height: 100%;
                        -webkit-transition: all 0.3s ease;
                        transition: all 0.3s ease;
                        &:hover {
                            background: var(--color-svetlo-zelenyy-2);
                            opacity: 1;
                        }
                    }
                    & span {
                        display: block;
                        font-family: var(--font-family);
                        font-weight: 300;
                        font-size: 14px;
                        color: var(--fony-plashek-seryy-2);
                    }
                }
            }
    
            &_stocks {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-orient: vertical;
                -webkit-box-direction: normal;
                    -ms-flex-direction: column;
                        flex-direction: column;
                gap: 10px;
                flex: 1 0 25%;
            }
        }
    
        &__stock {
            position: relative;
            border: 1px solid rgba(84, 84, 84, 0.2);
            border-radius: 20px;
            padding: 20px;
            background: var(--fony-plashek-svetlo-seryy);
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 70px;
            flex: 1 0 30%;

            &_inner {
                position: relative;
                z-index: 2;
            }
    
            &_date {
                font-weight: 500;
                font-size: 12px;
                line-height: 140%;
                color: var(--cveta-zelenyy);
                padding: 6px 12px;
                border-radius: 30px;
                background: rgba(195, 241, 194, 0.4);
                margin-bottom: 30px;
                width: fit-content;
            }
    
            &_title {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 18px;
                line-height: 133%;
                color: var(--tekst-temnyy);
                margin-bottom: 10px;
            }
    
            &_subtitle {
                font-size: 14px;
                line-height: 140%;
            }

            &_img {
                width: 100%;
                height: 100%;
                max-height: 60%;
                position: absolute;
                bottom: 0;
                left: 0;
                & img {
                    display: block;
                    height: 100%;
                    object-fit: cover;
                    object-position: top;
                    margin: 0 auto;
                }
            }
        }
    }
}


@media (max-width: 1100px) {
    .service-submenu-2 {
        position: static;
        width: 100%;
        //margin: 20px 0;
        transform: translateX(0);
    
        &__row {
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            gap: 10px;
            width: 100%;
            padding: 0;
        }
        
        & .header {
            &__submenu {
                &_inner {
                    width: 100%;
                    border-radius: 10px;
                    padding: 10px 0 20px;
                    gap: 0;
                    & ul {
                        display: grid;
                        grid-template-columns: repeat(2, 1fr);
                        -webkit-box-pack: justify;
                            -ms-flex-pack: justify;
                                justify-content: space-between;
                        gap: 10px 20px;
                        max-height: 345px;
                        padding: 0;
                        padding-right: 8px;
                    }
                }
                &_stocks {
                    flex-basis: 100%;
                    margin-top: 0;
                    order: 100;
                    flex-direction: row;
                    overflow-x: auto;
                    scroll-snap-type: x mandatory;
                    padding-bottom: 10px;
                    &::-webkit-scrollbar {
                        height: 4px;
                        background-color:  var(--fony-plashek-svetlo-seryy);
                    }
                    &::-webkit-scrollbar-thumb {
                        border-radius: 10px;
                        background-color: rgba(7, 57, 58, 0.3);
                    }
                    &::-webkit-scrollbar-track {
                        background-color:  var(--fony-plashek-svetlo-seryy);
                    }
                }
            }
        
            &__stock {
                position: relative;
                border-radius: 20px;
                padding: 20px;
                gap: 20px;
                &_date {
                    font-size: 12px;
                    margin-bottom: 20px;
                }
                &_title {
                    font-size: 18px;
                    margin-bottom: 10px;
                }
                &_subtitle {
                    font-size: 14px;
                }
                &_img {
                    display: none;
                }
            }
        }
    }
}

@media (max-width: 800px) {
    .service-submenu-2 .header {
        &__submenu {
            &_inner {
                padding: 0;
                & ul {
                    grid-template-columns: repeat(1, 1fr);
                    gap: 10px;
                    & a {
                        //border: none;
                        padding: 10px 12px;
                        font-size: 14px;
                        border-radius: 8px;
                    }
                }
            }
        }
    }
}


