/* submenu-3 */
.submenu-3 {
    position: absolute;
    top: calc(100% + 32px);
    left: -26px;
    border-radius: 20px;
    padding: 26px 6px;
    background: var(--belyy);
    min-width: 308px;

    & ul {
        display: flex;
        align-items: flex-start;
        flex-direction: column;
        gap: 22px;

        & li {
            display: flex;
            gap: 8px;
            width: 100%;

            & a {
                display: block;
                padding: 0;
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                color: var(--cveta-zelenyy);
                width: 100%;
                transition: all 0.3s ease;
                &:hover {
                    color: var(--cveta-zelenyy-2);
                }
            }
        }
    }
}

// media submenu-3
@media (max-width: 1260px) {
    .submenu-3 .container {
        display: contents;
    }
    .submenu-3 {
        position: static;
        padding: 0 20px;
        transform: translateX(0);
        -webkit-box-ordinal-group: 4;
            -ms-flex-order: 3;
                order: 3;
        min-width: 100%;
        margin-bottom: 14px;
        & ul {
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            gap: 12px;
            & li {
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                gap: 8px;
                width: 100%;
                & a {
                    padding: 0;
                    font-size: 14px;
                    width: 100%;
                    max-width: 100%;
                    min-height: auto;
                    -webkit-box-orient: horizontal;
                    -webkit-box-direction: normal;
                        -ms-flex-direction: row;
                            flex-direction: row;
                    -webkit-box-align: center;
                        -ms-flex-align: center;
                            align-items: center;
                }
            }
        }
    }
}
