// faq-5
.faq {
    &__grid {
        display: grid;
        grid-template-columns: 6.2fr 3.2fr;
        gap: 60px;
    }

    &__list {
        & li {
            border-bottom: 1px solid rgba(7, 57, 58, 0.2);
            &:first-child {
                border-top: 1px solid rgba(7, 57, 58, 0.2);
            }
        }
    }

    &__button {
        padding: 28px 0;
        text-align: left;
        &.accordion--active {
            & .faq__name{
                padding-bottom: 20px;
            }
            & .faq__btn-more {
                transform: rotate(180deg);
            }
        }
    }

    &__btn-more svg path {
        transition: all 0.3s ease;
    }
    &__btn-more {
        min-width: 40px;
        width: 40px;
        height: 40px;
        border: 1px solid var(--cveta-zelenyy);
        background-color: transparent;
        &::after {
            transform: rotate(135deg);
            margin: 0;
            margin-bottom: 2px;
        }
    }


    &__name {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 22px;
        line-height: 140%;
        color: var(--tekst-svetlyy);
        display: flex;
        justify-content: space-between;
        gap: 20px;
    }
    
    &__content {
        max-height: 0;
        overflow: hidden;
        transition: all 0.3s ease;
        &_text {
            & p {
                line-height: 140%;
                margin-bottom: 12px;
                color: var(--fony-plashek-seryy-2);
                &:last-child {
                    margin-bottom: 30px;
                }
            }
        }
    }

    &__editor {
        display: flex;
        align-items: center;

        &_border {
            padding-right: 18px;
            border-right: 1px solid rgba(0, 54, 73, 0.2);
            margin-right: 18px;
            flex: 1 0 35%;
        }

        &_who {
            display: flex;
            gap: 16px;
            align-items: center;
            flex: 1 0 38%;
        }

        &_img {
            display: block;
            border-radius: 100px;
            min-width: 60px;
            width: 60px;
            height: 60px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_text {
            font-weight: 400;
            font-size: 12px;
            line-height: 140%;
            color: var(--fony-plashek-seryy-2);
            margin-bottom: 4px;
        }

        &_bold {
            font-weight: 500;
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst-svetlyy);
        }

        & span {
            display: block;
        }

        &_item {
            display: flex;
            align-items: flex-start;
            gap: 10px;
            font-size: 14px;
            line-height: 140%;
            color: var(--fony-plashek-seryy-2);
        }
    }


    &__form {
        border: 2px solid var(--cveta-salatovyy);
        border-radius: 30px;
        padding: 32px;

        &_text {
            margin-bottom: 24px;
        }

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 20px;
        }

        &_inputs {
            display: flex;
            flex-direction: column;
            gap: 12px;   
        }

        &_input, &_textarea {
            border: 1px solid #07393a;
        }

        &_politic {
            color: var(--cveta-zelenyy);
        }
    }
}


// media faq-5
@media (max-width: 1150px) {
    .faq {
        &__grid {
            display: contents;
        }
        &__list {
            margin-bottom: 30px;
        }
        &__editor {
            &_border {
                flex: 1 0 30%;
            }
            &_who {
                flex: 1 0 38%;
            }
        }
    }
}
@media (max-width: 800px) {
    .faq {
        &__block_form {
            display: none;
        }
    }
}
@media (max-width: 688px) {
    .faq {
        &__button {
            padding: 20px 0 16px;
        }
        &__list {
            margin-bottom: 10px;
        }
        &__name {
            font-size: 16px;
        }
        &__btn-more {
            min-width: 30px;
            width: 30px;
            height: 30px;
        }
        &__editor {
            flex-direction: column;
            align-items: flex-start;
            gap: 4px;
            &_border {
                padding-right: 0;
                border-right: none;
                margin-right: 0;
            }
            &_who {
                margin-bottom: 10px;
                width: 100%;
                flex-direction: row-reverse;
                justify-content: space-between;
            }
            &_img {
                min-width: 50px;
                width: 50px;
                height: 50px;
            }
            &_item span {
                display: inline;
                font-weight: 400;
            }
        }
    }
}
