// intro-3
.intro-3 {
    padding: 80px 0 40px;
    background: url("./../img/intro/intro-bg-3.jpg") center no-repeat;
    background-size: cover;

    &__row {
        display: flex;
        gap: 60px;
    }

    &__main {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 50px;
        flex: 1 0 62%;

        & h1, h2 {
            color: var(--tekst-belyy);
            margin-bottom: 16px;
        }
        &_subtitle p {
            color: var(--tekst-belyy);
            &:not(:last-child) {
                margin-bottom: 14px;
            }
            & span {
                border-radius: 8px;
                padding: 4px 6px;
                backdrop-filter: blur(40px);
                background: var(--tekst-belyy);
                font-size: 18px;
                font-weight: 700;
                color: var(--cveta-zelenyy);
            }
        }

        &_bottom {
            display: flex;
            gap: 16px;
        }
        &_networks {
            display: flex;
            gap: 8px;
            & a {
                border-radius: 40px;
                width: 50px;
                height: 50px;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 8px;
                font-weight: 500;
                font-size: 14px;
                line-height: 140%;
                text-align: center;
                color: var(--tekst-belyy);
                & span {
                    display: none;
                }
            }
        }
        &_network-wa {
            background: #5fcc6c;
        }
        &_network-tg {
            background: #3daede;
        }
    }

    &__cards ul {
        margin-top: 30px;
        display: flex;
        gap: 16px;
        & li {
            flex-basis: 250px;
            border: 1px solid #82B9A8;
            backdrop-filter: blur(14px);
            background: rgba(73, 73, 73, 0.1);
            border-radius: 20px;
            padding: 24px;
            font-weight: 400;
            line-height: 140%;
            color: var(--tekst-belyy);
            & img {
                margin-bottom: 20px;
            }
        }
    } 

    &__right {
        border-radius: 30px;
        padding: 10px;
        backdrop-filter: blur(36.099998474121094px);
        background: rgba(255, 255, 255, 0.04);
        flex: 1 0 32%;
    }

    &__form {
        padding: 20px 30px;

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 24px;
            line-height: 140%;
            color: var(--tekst-belyy);
            margin-bottom: 8px;
        }
        &_phone {
            margin-bottom: 10px;
        }
        &_subtitle {
            color: var(--tekst-belyy);
            margin-bottom: 30px;
        }
        &_inner {
            display: flex;
            flex-direction: column;
            gap: 12px;
        }
        &_input {
            border: 1px solid rgba(255, 255, 255, 0.1);
            background: rgba(255, 255, 255, 0.04);
            color: rgba(247, 248, 250, 0.8);
            &::placeholder {
                color: rgba(247, 248, 250, 0.8);
            }
            &:hover, &:active, &:focus {
                background: rgba(255, 255, 255, 0.06);
            }
        }
        &_politic {
            color: rgba(255, 255, 255, 0.6);
        }
    }

    &__label {
        border-radius: 20px;
        padding: 20px 24px;
        background: var(--cveta-salatovyy);

        & ul {
            display: flex;
            gap: 20px;
            padding-bottom: 16px;
            border-bottom: 1px solid rgba(7, 57, 58, 0.2);
            margin-bottom: 16px;
        }

        & li {
            flex: 1 0 40%;
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 14px;
            font-size: 14px;
            color: var(--cveta-zelenyy);
            &:first-child {
                padding-right: 20px;
                border-right: 1px solid rgba(32, 97, 117, 0.1);
            }
        }

        & p {
            font-weight: 400;
            font-size: 14px;
            line-height: 140%;
            color: rgba(15, 86, 88, 0.8);
            text-align: center;
        }
    }
}

.intro-service {
    padding: 20px 0 40px;
}

// media intro-3
@media (max-width: 1140px) {
    .intro-3 {
        &__row {
            flex-direction: column;
            gap: 30px;
        }
    }
}
@media (max-width: 688px) {
    .intro-3 {
        padding: 40px 0 20px;
        &__main {
            gap: 0;
            & h1, h2 {
                margin-bottom: 20px;
            }
            &_subtitle p {
                &:not(:last-child) {
                    margin-bottom: 20px;
                }
                & span {
                    font-size: 16px;
                }
            }
            &_btn {
                width: 100%;
            }
            &_top {
                display: contents;
            }
            &_bottom {
                flex-direction: column;
                gap: 12px;
                margin-top: 30px;
            }
            &_networks a {
                flex: 1 0 40%;
                & span {
                    display: block;
                }
            }
        }
        &__cards {
            order: 2;
            & ul {
                flex-direction: column;
                gap: 10px;
                & li {
                    padding: 20px 24px;
                    display: flex;
                    gap: 16px;
                    align-items: center;
                    flex-basis: auto;
                    & img {
                        width: 25px;
                        height: 25px;
                        margin: 0;
                    }
                }
            }
        } 
        &__right {
            border-radius: 30px;
            padding: 10px;
        }
        &__form {
            padding: 14px;
            &_title {
                font-size: 20px;
            }
        }
        &__label ul li {
            flex-direction: column;
            align-items: flex-start;
        }
    }
    .intro-service {
        padding: 20px 0;
    }
}