// filials-3
.filials {
    &__row {
        display: flex;
        justify-content: space-between;
        gap: 50px;
    }


    &__list {
        flex-basis: 64%;
        & ul {
            display: flex;
            flex-wrap: wrap;
            gap: 16px 20px;
            & li {
                width: calc(50% - 10px);
                & a {
                    display: flex;
                    align-items: center;
                    gap: 16px;
                    font-family: var(--second-family);
                    font-weight: 500;
                    font-size: 22px;
                    line-height: 140%;
                    color: var(--tekst-temnyy);
                    padding-bottom: 16px;
                    border-bottom: 1px solid rgba(107, 107, 107, 0.2);
                    transition: all 0.3s ease;
                    &::before {
                        content: "";
                        display: block;
                        background: url("./../img/icons/filials-loc.svg");
                        background-size: cover;
                        min-width: 22px;
                        width: 22px;
                        height: 26px;
                    }
                    &:hover {
                        opacity: 0.6;
                    }
                }
            }
        }
    }
}


// media filials-3
@media (max-width: 1120px) {
    .filials {
        &__row {
            flex-direction: column;
            gap: 50px;
        }
    }
}
@media (max-width: 720px) {
    .filials {
        &__row {
            gap: 20px;
        }
        &__list {
            flex-basis: 100%;
            & ul {
                flex-direction: column;
                & li {
                    width: 100%;
                    & a {
                        font-size: 18px;
                        &::before {
                            min-width: 17.5px;
                            width: 17.5px;
                            height: 20.5px;
                        }
                    }
                }
            }
        }
    }
}