// jobs-2
.jobs {
    &__title-h1 {
        margin-bottom: 50px;
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 10px;
        list-style-type: none;
        counter-reset: item;
    }

    &__item {
        border-bottom: 1px solid rgba(15, 86, 88, 0.2);
    }
    
    &__button {
        padding: 20px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 40px;

        &::before {
            content: "("counter(item, decimal-leading-zero)")"; 
            counter-increment: item;
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 34px;
            line-height: 100%;
            color: var(--cveta-salatovyy);
        }

        &.accordion--active {
            margin-bottom: 30px;
            & .jobs__button_btn-more {
                transform: rotate(180deg);
            }
        }

        &_text {
            flex: 1 0 70%;
            display: flex;
            align-items: start;
            gap: 20px;
        }

        &_name {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            text-align: left;
        }
    
        &_salary {
            min-width: max-content;
            border-radius: 40px;
            padding: 8px 16px;
            background: rgba(195, 241, 194, 0.4);
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 18px;
            line-height: 133%;
            text-align: center;
            color: var(--cveta-zelenyy);
        }

        &_btn-more svg path {
            transition: all 0.3s ease;
        }
        &_btn-more {
            min-width: 40px;
            width: 40px;
            height: 40px;
            border: 1px solid var(--cveta-zelenyy);
            background-color: transparent;
            &::after {
                transform: rotate(135deg);
                margin: 0;
                margin-bottom: 2px;
            }
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        padding: 0;
        display: flex;
        flex-direction: column;
        gap: 30px;
        padding-left: 100px;
        
        &.accordion__content--active {
            margin-bottom: 30px;
        }

        &_labels {
            display: flex;
            gap: 25px;
            width: fit-content;
        }

        &_label {
            display: flex;
            align-items: center;
            gap: 15px;
            & p:first-child {
                font-weight: 500;
                color: var(--cveta-zelenyy);
            }
            & p:last-child {
                font-size: 14px;
            }
            &:not(:last-child) {
                padding-right: 25px;
                border-right: 1px solid rgba(15, 86, 88, 0.2);
            }
        }

        &_icon {
            border-radius: 5px;
            padding: 8px;
            width: 36px;
            height: 36px;
            background: var(--fony-plashek-svetlo-seryy);
            display: flex;
            justify-content: center;
            align-items: center;
        }

        &_block {
            & p {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 18px;
                line-height: 133%;
                color: var(--cveta-zelenyy);
                margin-bottom: 15px;
            }
            & ul {
                display: flex;
                flex-direction: column;
                gap: 12px;
                & li {
                    display: flex;
                    gap: 10px;
                    &::before {
                        content: "";
                        display: block;
                        min-width: 18px;
                        width: 18px;
                        height: 3px;
                        border-radius: 10px;
                        background: var(--cveta-zelenyy-2);
                        margin-top: 7px;
                    }
                }
            }
        }

        &_bottom {
            border-radius: 20px;
            padding: 30px 40px;
            background: var(--cveta-zelenyy-2);
            display: flex;
            align-items: center;
            gap: 40px;
        }

        &_text {
            flex: 1 0 50%;
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst-belyy);
            padding-right: 40px;
            border-right: 1px solid rgba(255, 255, 255, 0.2);
        }

        &_contacts {
            flex: 1 0 40%;
            display: flex;
            flex-direction: column;
            gap: 6px;
            color: #ebebeb;
            & a {
                text-wrap: nowrap;
                font-weight: 700;
                color: var(--tekst-belyy);
            }
        }
    }
}


// media jobs-2
@media (max-width: 740px) {
    .jobs {
        &__title-h1 {
            margin-bottom: 30px;
        }
        &__list {
            gap: 30px;
        }
        &__button {
            padding: 0;
            padding-bottom: 30px;
            align-items: start;
            gap: 20px;
            &.accordion--active {
                margin-bottom: 0;
            }
            &::before {
                display: none;
            }
            &_text {
                flex-direction: column;
                gap: 16px;
            }
            &_name {
                font-size: 16px;
            }
            &_salary {
                padding: 6px 14px;
                font-size: 16px;
            }
            &_btn-more {
                min-width: 30px;
                width: 30px;
                height: 30px;
                &::after {
                    transform: rotate(135deg);
                    margin: 0;
                    margin-bottom: 2px;
                }
            }
        }
        &__content {
            padding: 0;
            gap: 24px;
            &_labels {
                flex-direction: column;
                gap: 14px;
                width: 100%;
            }
            &_label:not(:last-child) {
                padding-right: 0;
                border-right: none;
                padding-bottom: 14px;
                border-bottom: 1px solid rgba(15, 86, 88, 0.2);
            }
            &_block {
                & p {
                    font-size: 14px;
                }
                & ul li::before {
                    min-width: 12px;
                    width: 12px;
                    height: 2px;
                    margin-top: 8px;
                }
            }
            &_bottom {
                border-radius: 20px;
                padding: 20px;
                flex-direction: column;
                align-items: stretch;
                gap: 16px;
            }
            &_text {
                font-size: 18px;
                padding-right: 0;
                border-right: none;
                padding-bottom: 16px;
                border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            }
        }
    }
}
