// stocks-6
.stocks {
    &__title-h1 {
        margin-bottom: 50px;
    }

    &__btns {
        display: flex;
        align-items: center;
        gap: 30px;
        & p {
            font-size: 14px;
            padding-right: 30px;
            border-right: 1px solid rgba(15,86,88,0.2);
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
    }

    &__card {
        border: 1px solid rgba(107, 107, 107, 0.2);
        border-radius: 20px;
        padding: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        &:first-child{
            grid-row: span 2;
            grid-column: span 2;
            & .stocks__card_btn {
                width: fit-content;
            }
        }

        &_date {
            border-radius: 30px;
            padding: 8px 14px;
            background: rgba(195, 241, 194, 0.5);
            font-size: 14px;
            font-weight: 500;
            color: rgba(7, 57, 58, 0.8);
            width: fit-content;
            margin-bottom: 20px;
        }

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 10px;
        }

        &_subtitle {
            margin-bottom: 30px;
        }

        &_btn {
            width: 100%;
        }

        &_picture {
            & img {
                border-radius: 10px;
                width: 100%;
                height: auto;
                object-fit: cover;
            }
        }
    }
}


// media stocks-6
@media (max-width: 1150px) {
    .stocks {
        &__cards {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
        &__card {
            &:first-child{
                grid-row: span 1;
                grid-column: span 1;
                & .stocks__card_btn {
                    width: 100%;
                }
                & .stocks__card_picture {
                    display: none;
                }
            }
        }
    }
}
@media (max-width: 800px) {
    .stocks {
        &__btns {
            gap: 10px;
            & p {
                flex-grow: 1;
                width: 100%;
                padding-right: 10px;
            }
            & a {
                width: auto;
                flex: 1 0 auto;
            }
        }
    }
}
@media (max-width: 700px) {
    .stocks {
        &__title-h1 {
            margin-bottom: 30px;
        }
        
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 10px;
        }
    
        &__card {
            border-radius: 20px;
            padding: 20px;
            &:first-child{
                & .stocks__card_picture {
                    display: block;
                    & img {
                        max-height: 200px;
                    }
                }
            }
            &_date {
                padding: 6px 12px;
                font-size: 12px;
                margin-bottom: 30px;
            }
            &_title {
                font-size: 18px;
            }
        }
    }
}