// guarantees-2
.guarantees {
    &__row {
        display: flex;
        justify-content: space-between;
        gap: 60px;
    }

    &__title {
        margin-bottom: 50px;
    }

    &__cards {
        flex-grow: 1;
        & ul {
            display: flex;
            flex-direction: column;
            gap: 20px;
            & li {
                border: 1px solid rgba(111, 111, 111, 0.2);
                border-radius: 30px;
                padding: 20px;
                padding-left: 68px;
                color: var(--tekst-svetlyy);
                position: relative;
                & p:first-child {
                    font-family: var(--second-family);
                    font-weight: 600;
                    font-size: 22px;
                    line-height: 140%;
                    color: var(--cveta-zelenyy);
                    margin-bottom: 14px;
                }
                &::before {
                    content: "";
                    background-image: url("./../img/icons/guarantees-checkmark.svg");
                    background-size: cover;
                    display: block;
                    width: 32px;
                    height: 32px;
                    position: absolute;
                    top: 20px;
                    left: 20px;
                }
            }
        }
    }

    &__img {
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 30px;
        }
    }
}


// media guarantees-2
@media (max-width: 920px) {
    .guarantees {
        &__row {
            flex-direction: column;
            gap: 20px;
        }
        &__img {
            & img {
                max-height: 460px;
            }
        }
    }
}
@media (max-width: 600px) {
    .guarantees {
        &__title {
            margin-bottom: 30px;
        }
        &__cards {
            & ul {
                gap: 10px;
                & li {
                    border-radius: 20px;
                    padding: 14px;
                    padding-left: 52px;
                    &::before {
                        width: 24px;
                        height: 24px;
                        top: 14px;
                        left: 14px;
                    }
                    & p:first-child {
                        font-size: 18px;
                        margin-bottom: 8px;
                    }
                }
            }
        }
        &__img {
            & img {
                border-radius: 20px;
                max-height: 300px;
            }
        }
    }
}