// popup-search-2

.popup-search {
    &__body {
        display: flex;
        justify-content: flex-end;
        align-items: stretch;
        padding: 0;
    }
    &__content {
        //border-radius: 0 0 20px 20px;
        padding: 70px 0;
        background: var(--belyy);
        max-width: 100%;
        height: max-content;
        max-height: 100vh;
    }

    &__top {
        display: flex;
        justify-content: space-between;
        position: relative;
        margin-bottom: 40px;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 34px;
        line-height: 140%;
        text-align: center;
        color: var(--tekst-temnyy);
    }



    &__search {
        margin-top: 40px;
        &-result {
            max-height: 666px;
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        & li a {
            display: block;
            border: 1px solid var(--cveta-temno-zelenyy);
            border-radius: 30px;
            padding: 10px 20px;
            font-weight: 500;
            font-size: 16px;
            color: var(--cveta-temno-zelenyy);
            transition: all 0.3s ease;
            &:hover {
                opacity: 0.6;
            }
        }
    }

    &__list_result {
        & li a {
            display: block;
            border-radius: 20px;
            padding: 18px 60px 18px 16px;
            margin: 0 4px;
            &:hover {
                background: var(--fony-plashek-svetlo-seryy);
            }
            & p {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 18px;
                line-height: 133%;
                color: var(--tekst-temnyy);
                margin-bottom: 6px;
            }
            & span {
                font-size: 14px;
                color: var(--fony-plashek-seryy-2);
            }
        }
    }

    &__close {
        width: 50px;
        height: 50px;
        top: 0;
        right: 0;
    }
}

// media popup-search-2
@media (max-width: 1500px) {
    .popup-search {
        &__form {
            //display: block;
            //width: calc(100% - 64px);
        }

    }
}
@media (max-width: 750px) {
    .popup-search {
        &__body {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 20px;
        }
        &__content {
            border-radius: 20px;
            padding: 20px 0 80px;
            max-width: 520px;
        }
        &__top {
            margin-bottom: 24px;
        }
        &__title {
            font-size: 24px;
        }
        &__search {
            display: none;
        }
        &__search-result {
            max-height: 450px;
        }
        &__list {
            flex-direction: column;
            flex-wrap: nowrap;
            gap: 0;
            max-height: 500px;
            overflow-y: auto;
            padding-right: 10px;
            &::-webkit-scrollbar {
                width: 4px;
                background-color: var(--izobrazhenie);
                padding: 5px;
            }
            &::-webkit-scrollbar-thumb {
                border-radius: 10px;
                background: var(--color-zelenyy);
            }
            & li a {
                display: block;
                border: none;
                border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
                border-radius: 0;
                padding: 16px 0;
                font-size: 14px;
                color: var(--color-zelenyy);
                &:hover {
                    color: var(--color-temno-zelenyy);
                }
            }
        } 
        &__list_result {
            & li a {
                padding: 14px;
                margin: 0 4px 0 0;
            }
        }
        &__form, &__search, &__search-result {
            width: 100%;
        }
        &__close {
            width: 25px;
            height: 25px;
            top: 4px;
        }
    }
}
