/* Inter */
@font-face {
    font-family: "Inter";
    font-style: normal;
    font-display: swap;
    font-weight: 300;
    src: local("Inter"), url("../fonts/Inter-Light.woff2") format("woff2"), url("../fonts/Inter-Light.woff") format("woff");
}
