/* linking */

.linking__title {
    margin-bottom: 50px;
}

.linking__row {
    display: flex;
    gap: 60px;
}

.linking__list {
    flex: 1 0 45%;
    & ul {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
        & li {
            width: calc(50% - 10px);
            & a {
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                border-radius: 12px;
                padding: 12px 16px;
                background: var(--fony-plashek-svetlo-seryy);
                font-weight: 400;
                color: var(--tekst-temnyy);
                &::after {
                    border-top: 1px solid var(--cveta-zelenyy-2);
                    border-right: 1px solid var(--cveta-zelenyy-2);
                    margin: 0;
                }
            }
        }
    }
}

.linking__img {
    flex: 1 0 45%;
    &  img {
        border-radius: 20px;
        width: 100%;
        height: 100%;
        max-height: 330px;
        object-fit: cover;
    }
}


/* linking media */

@media (max-width: 1024px) {
    .linking__img {
        display: none;
    }
}

@media (max-width: 688px) {
    .linking__title {
        margin-bottom: 30px;
    }
    .linking__list {
        & ul {
            flex-direction: column;
            gap: 8px;
            & li {
                width: 100%;
            }
        }
    }
}
