/* Navigation */
.navigation__inner {
    width: 100%;
    height: 100%;
    border: 1px solid rgba(107, 107, 107, 0.2);
    border-radius: 30px;
    padding: 60px;
    display: flex;
    justify-content: space-between;
    gap: 50px;
    position: relative;
    z-index: 1;
    //margin-bottom: 20px;
}

.navigation__title {
    color: var(--cveta-zelenyy);
    margin-bottom: 30px;
}

.navigation__wrap {
    flex-basis: calc(50% - 25px);
    padding-right: 50px;
    border-right: 1px solid rgb(84,84,84,0.2);
}


.navigation__list {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.navigation__link {
    position: relative;
    transition: all 0.3s ease;
    font-weight: 600;

    &:hover {
        text-decoration: none;
    }
}

.navigation__item {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.56;
}

.navigation__list .nav-title-h3 .navigation__link,
.navigation__list .nav-title-h4 .navigation__link,
.navigation__list .nav-title-h5 .navigation__link {
    padding-left: 52px;
    font-weight: 300;
    color: var(--cveta-zelenyy);
    transition: color 0.3s ease;
    position: relative;
    &:after {
        content: '';
        position: absolute;
        left: 20px;
        top: 8px;
        width: 12px;
        height: 3px;
        border-radius: 10px;
        background: var(--cveta-salatovyy);
    }
    &:hover {
        color: var(--cveta-zelenyy-2);
    }
}

.navigation__list .nav-title-h2 .navigation__link {
    color: var(--cveta-zelenyy);
    font-weight: 500;
    position: relative;
    transition: color 0.3s ease;
    &:hover {
        color: var(--cveta-zelenyy-2);
    }
}


.dock-check__inner {
    display: flex;
    flex-direction: column;
    gap: 20px;
    flex-basis: calc(50% - 10px);

    &>* {
        flex-basis: calc(50% - 10px);
    }
}

.doc-check {
    border-radius: 30px;
    padding: 30px;
    background: var(--fony-plashek-svetlo-seryy);
    transition: border-color 0.3s ease;
    color: var(--tekst-svetlyy);
    transition: all 0.3s ease;
    &:hover {
        background: rgb(195,241,194,0.2)
    }
}

.doc-check__name {
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 22px;
    line-height: 140%;
    color: var(--cveta-zelenyy);
}

.doc-check__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 10px;
    margin-bottom: 16px;
    border-bottom: 1px solid rgba(190, 219, 215, 0.4);
    font-size: 14px;
    color: var(--cveta-zelenyy);
}

.doc-check__content {
    display: flex;
    gap: 20px;
}

.doc-check__picture {
    max-width: 142px;
    max-height: 142px;
    border-radius: 20px;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle, rgba(255,255,255,1) 0%, rgba(229,241,239,1) 100%);

    & img {
        object-fit: cover;
        border-radius: 10px;
        width: 100%;
        height: 100%;
        object-position: top;
    }
}

.doc-check__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 10px;
}

.doc-check__post {
    position: relative;
    padding-left: 20px;

    &:after {
        content: '';
        position: absolute;
        top: 3px;
        left: 0;
        width: 16px;
        height: 16px;
        background: url("./../img/icons/doctor-2.svg") no-repeat center center / cover;
    }
}


.doc-check__stage {
    position: relative;
    padding-left: 20px;

    &:after {
        content: '';
        position: absolute;
        top: 3px;
        left: 0;
        width: 16px;
        height: 16px;
        background: url("./../img/icons/calendar-2.svg") no-repeat center center / cover;
    }
}

/* Navigation media */


@media (max-width: 1200px) {
    .navigation__inner {
        padding: 20px;
        border-radius: 20px;
    }

    .doc-check {
        padding: 20px;
        border-radius: 20px;
    }

    .doc-check__top {
        padding-bottom: 10px;
        margin-bottom: 16px;
        flex-wrap: wrap;
    }

    .doc-check__content {
        gap: 10px;
    }
}

@media (max-width: 1024px) {
    .navigation__inner {
        flex-direction: column;
        gap: 20px;
    }
    .navigation__wrap {
        padding-right: 0;
        border-right: none;
        padding-bottom: 20px;
        border-bottom: 1px solid rgb(84,84,84,0.2);
    }
}

@media (max-width: 500px) {
    .navigation__list .nav-title-h3 .navigation__link,
    .navigation__list .nav-title-h4 .navigation__link,
    .navigation__list .nav-title-h5 .navigation__link {
        padding-left: 32px;
        &:after {
            left: 10px;
            top: 8px;
            width: 12px;
            height: 3px;
            border-radius: 10px;
            background: var(--cveta-salatovyy);
        }
    }
    .doc-check__picture,
    .doc-check__picture img {
        max-width: 50px;
        max-height: 50px;
    }
    .doc-check {
        border: none;
    }
    .doc-check__top {
        font-size: 12px;
    }
    .doc-check__content {
        flex-wrap: wrap;
        align-items: center;
        gap: 16px;
    }
    .doc-check__info {
        display: contents;
    }
    .doc-check__name {
        flex: 1 0 60%;
        font-size: 18px;
    }
    .doc-check__post {
        flex: 1 0 100%;
    }
}
