// form-1
.form-1 {
    padding: 100px 0;
    background: url("./../img/form/form-bg.jpg") var(--cveta-zelenyy);
    background-size: cover;
    overflow: hidden;

    &__row {
        display: flex;
        width: 100%;
        position: relative;
    }

    &__wrapper {
        width: 60%;
        position: relative;
        z-index: 2;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 44px;
        line-height: 110%;
        color: var(--tekst-belyy);
        margin-bottom: 40px;
        & span {
            color: var(--cveta-salatovyy);
        }
    }

    &__subtitle {
        font-weight: 400;
        line-height: 140%;
        color: var(--tekst-belyy);
        display: flex;
        gap: 25px;
        margin-bottom: 40px;
    }

    &__content {
        border-radius: 20px;
        padding: 40px;
        background: rgba(255, 255, 255, 0.8);
        font-weight: 400;
        color: var(--fony-plashek-seryy-2);
    }

    &__text {
        font-size: 16px;
        margin-bottom: 30px;
    }

    &__inner {
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
    }

    &__input, &__btn {
        flex: 1 0 25%;
    }

    &__btn {
        background: var(--cveta-zelenyy);
        color: var(--tekst-belyy);
    }

    &__politic {
        flex-basis: 100%;
    }

    &__img {
        position: absolute;
        bottom: -100px;
        right: -40px;
    }
}

// media form-1
@media(max-width: 1000px) {
    .form-1 {
        &__wrapper {
            width: 100%;
        }
        &__img {
            display: none;
        }
    }
}
@media(max-width: 688px) {
    .form-1 {
        padding: 40px 0;
        &__title {
            font-size: 24px;
            margin-bottom: 20px;
        }
        &__subtitle {
            flex-direction: column;
            gap: 40px;
            margin-bottom: 20px;
        }
        &__content {
            border-radius: 10px;
            padding: 20px;
        }
        &__text {
            margin-bottom: 20px;
        }
        &__input, &__btn {
            flex: 1 0 100%;
        }
    }
}