// licenses-2
.licenses {
    &__row {
        display: flex;
        justify-content: space-between;
        gap: 20px;
    }

    &__top {
        margin-bottom: 50px;
    }

    &__info {
        max-width: 35%;
        border-radius: 30px;
        padding: 40px;
        background: rgba(195, 241, 194, 0.4);
        color: var(--tekst-svetlyy);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        & p {
            margin-bottom: 20px;
        }
    }

    &__line {
        border-bottom: 1px solid rgba(7, 57, 58, 0.2);
    }

    &__label {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 22px;
        line-height: 140%;
        color: var(--cveta-zelenyy);
        margin-bottom: 0 !important;
    }

    &__swiper {
        &-slide {
            border: 4px solid var(--fony-plashek-svetlo-seryy);
            border-radius: 30px;
            overflow: hidden;
            position: relative;
            max-height: 430px;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top;
                border-radius: 10px;
            }
        }

        &_label {
            font-weight: 500;
            font-size: 14px;
            line-height: 140%;
            text-align: center;
            color: var(--cveta-zelenyy);
            border-radius: 0 0 10px 10px;
            padding: 15px 25px;
            background: var(--fony-plashek-svetlo-seryy);
            width: 100%;
            position: absolute;
            bottom: 0;
            right: 0;
            left: 0;
            z-index: 10;
        }
    }

    &__right {
        max-width: 65%;
    }

    &__swiper-pagination {
        display: none;
    }
}

// media licenses-2
@media (max-width: 1150px) {
    .licenses {
        &__row {
            flex-direction: column;
            gap: 30px;
        }
        &__info {
            max-width: 100%;
            border-radius: 20px;
            padding: 30px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 30px;
            & p {
                margin-bottom: 20px;
            }
        }
        &__label {
            font-size: 18px;
        }
        &__swiper {
            &-slide {
                border-radius: 10px;
                overflow: hidden;
                position: relative;
                max-height: none;
            }
        }
        &__swiper_label {
            border-radius: 8px;
        }
        &__right {
            max-width: 100%;
        }
    }
}
@media (max-width: 800px) {
    .licenses {
        overflow: hidden;
        &__title {
            margin-bottom: 30px;
        }
        &__swiper-btns {
            display: none;
        }
        &__swiper-pagination {
            display: block;
        }
        &__info {
            border-radius: 10px;
            padding: 20px;
            gap: 20px;
        }
        &__label {
            font-size: 16px;
        }
        &__right {
            margin-bottom: 20px;
        }
        &__swiper_label {
            padding: 12px;
        }
        &__swiper-scrollbar {
            display: block;
        }
    }
}