// prices-1
.prices {
    &__flex {
        display: flex;
        gap: 50px;
    }

    &__left {
        display: block;
        flex-basis: 40%;
        margin: 0;
        &_text {
            margin-bottom: 24px;
        }
    }

    &__inner {
        flex-basis: 65%;
    }

    &__title {
        margin-bottom: 30px;
    }

    &__btn {
        display: block;
        width: 50%;
    }

    &__table {
        display: block;
        width: 100%;

        &_body {
            display: block;
            width: 100%;
        }

        &_item {
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 20px;
            color: var(--fony-plashek-seryy-2);
            padding-top: 25px;
            border-bottom: 1px solid #c7d6dc;
            &:first-child {
                border-top: 1px solid #c7d6dc;
            }
        }

        &_name {
            flex: 1 0 80%;
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 22px;
            line-height: 140%;
            color: #003649;
        }

        &_cost {
            border-right: 1px solid #c7d6dc;
            padding-right: 20px;
            font-weight: 700;
            & span {
                font-weight: 300;
            }
        }

        &_btn {
            padding: 12px 20px;
        }
        &_btn-circle {
            width: 40px;
            height: 40px;
            background: #fff;
            border: 1px solid var(--cveta-zelenyy);
            &::after {
                width: 10px;
                height: 10px;
                transform: rotate(135deg);
                margin: 0 0 4px 1px;
            }
        }

        &_info {
            flex-basis: 100%;
            display: flex;
            flex-direction: column;
            gap: 10px;
        }
        &_description {
            border-radius: 20px;
            padding: 30px;
            background: var(--fony-plashek-svetlo-seryy);
            & p:first-child {
                font-weight: 500;
                color: var(--cveta-zelenyy);
                margin-bottom: 14px;
            }
        }
    }

    // page-prises
    &__tab {
        &_btns {
            display: flex;
            flex-direction: column;
            gap: 12px;
            border-top: 1px solid rgba(7, 57, 58, 0.2);
            border-bottom: 1px solid rgba(7, 57, 58, 0.2);
            padding-top: 26px;
            padding-bottom: 30px;
            margin-bottom: 30px;
            & p {
                margin-bottom: 6px;
            }
        }

        &_btn {
            display: flex;
            align-items: center;
            gap: 12px;
            font-weight: 500;
            line-height: 140%;
            color: rgb(27, 29, 29);
            opacity: 0.6;
            transition: all 0.3s ease;
            &::before {
                content: "";
                display: block;
                border-radius: 22px;
                width: 8px;
                height: 8px;
                background: rgba(16, 153, 110, 0.9);
            }
            &.tab__btn--active {
                opacity: 1;
            }
        }
    }

    &__pays {
        & p {
            font-weight: 400;
            margin-bottom: 12px;
        }
        &_list {
            display: flex;
            gap: 10px;
            & img {
                display: block;
                width: 80px;
                height: 50px;
                padding: 15px 20px;
                border-radius: 10px;
                background-color: var(--fony-plashek-svetlo-seryy);
            }
        }
    }
}

.accordion--active.prices__table_item {
    padding-bottom: 20px;
}


// media prices-1
@media (max-width: 950px) {
    .prices {
        &__flex {
            flex-direction: column;
            gap: 30px;
        }

        &__table {
            &_item {
                width: 100%;
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                gap: 16px;
                justify-content: space-between;
                color: var(--tekst);
                padding-top: 30px;
                border-bottom: 1px solid #c7d6dc;
            }
            &_name {
                flex: 1 0 75%;
                order: 1;
            }
            &_cost {
                flex: 1 0 45%;
                order: 3;
                & span {
                    display: none;
                }
            }
            &_btns {
                display: contents;
            }
            &_btn {
                order: 5;
                flex: 1 0 45%;
                width: 100%;
            }
            &_btn-circle {
                order: 2;
            }
            &_info {
                order: 6;
            }
        }
    }
    .accordion--active.prices__table_item {
        padding-bottom: 30px;
    }
}
@media (max-width: 688px) {
    .prices {
        &__left {
            display: contents;
            &_text {
                margin: 0;
            }
        }
        &__btn {
            width: 100%;
        }
        &__table {
            &_name {
                font-size: 16px;
            }
            &_cost {
                padding: 10px;
            }
            &_btn-circle {
                width: 30px;
                height: 30px;
            }
            &_info {
                gap: 15px;
            }
            &_description {
                border-radius: 20px;
                padding: 20px;
            }
        }
        &__tab {
            &_btns {
                margin-bottom: 0;
            }
        }
    }
}
