/* header  */

.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 100;
    background-color: #fff;
    backdrop-filter: blur(12px);
    background: rgba(255, 255, 255, 0.8);
}

main {
    margin-top: 232px;
    transition: margin-top 0.3s ease;
}

.header__logo {
    display: flex;
    gap: 8px;

    font-weight: 400;
    font-size: 12px;
    line-height: 140%;
    color: var(--cveta-zelenyy);
}
.logo__picture {
    max-width: 96px;
    min-width: 82px;
    width: 100%;
    height: 100%;
    padding-right: 8px;
    border-right: 1px solid var(--cveta-zelenyy);
    & img {
        width: 100%;
        height: 100%;
    }
}

.header__main, .header__top, .header__bottom {
    transition: all 0.3s ease;
    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
}

.header__main.hide, .header__bottom.hide {
    padding: 0;
}

.header__block {
    padding: 20px 0;
    &:not(:first-child) {
        border-top: 1px solid rgba(15,86,88,0.2);
    }
}



/* header__top  */

.header__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
    transition: all 0.3s ease;

    &_right, &_btns {
        display: flex;
        gap: 10px;
    }
    &_btns button {
        width: max-content;
        padding: 13px 20px;
    }
}

.header__search {
    position: relative;
    width: 100%;
    &-wrapper {
        position: relative;
        min-width: 262px;
        max-width: 262px;
        width: 100%;
    }
    & input {
        width: 100%;
        border-radius: 40px;
        padding: 13px 5px 13px 20px;
        background: var(--fony-plashek-svetlo-seryy);
        opacity: 0.7;
        &:hover, &:active {
            opacity: 1;
        }
    }
    & img {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
    }
    &_result {
        position: absolute;
        top: calc(100% + 4px);
        right: 0;
        min-width: 200px;
        width: 100%;
        z-index: 20;
        border-radius: 10px;
        padding: 4px;
        background: var(--belyy);
        margin: 0 !important;

        & li a {
            border-radius: 20px;
            padding: 18px 20px 18px 16px !important;
            margin: 0 !important;
            border: none !important;
            &:hover {
                background: var(--color-svetlo-zelenyy-2);
            }
        }

        & .no-results-message {
            padding: 14px 16px;
        }
    }

    &_item {
        & > p {
            font-weight: 600;
            font-size: 14px;
            line-height: 143%;
            color: var(--tekst);
            margin-bottom: 8px;
        }
        &-bottom {
            display: flex;
            justify-content: space-between;
            gap: 15px;
            font-size: 12px;
            line-height: 140%;
            color: var(--tegi);
            opacity: 0.7;
            & > p span {
                color: #000000 !important;
                display: inline !important;
                font-size: 12px !important;
                font-weight: 500;
                &::before {
                    content: "" !important;
                }
            }
        }
    }

}


/* header__main */
.header__main {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
}

.header__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 40px;
}

.header__info_item {
    display: flex;
    gap: 10px;
}

.header__info_icon {
    border-radius: 40px;
    width: 40px;
    height: 40px;
    padding: 11px;
    background: rgba(195, 241, 194, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__info_text {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: var(--cveta-zelenyy);
}

.header__info_text p:first-child {
    font-size: 12px;
    color: var(--fony-plashek-seryy-2);
    margin-bottom: 2px;
}

.header__info_location button {
    font-weight: 500;
    text-decoration: underline;
}

.header__info_phone a {
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 18px;
    color: var(--cveta-zelenyy);
    text-wrap: nowrap;
}
.header__info_phone a:hover, .header__info_location button:hover {
    text-decoration: underline;
}

.header__links {
    display: flex;
    align-items: center;
    gap: 12px;
}
.header__eye {
    min-width: 26px;
    height: 26px;
}
.header__networks {
    display: flex;
    gap: 6px;
}
.header__network {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 5px;
    background: var(--fony-plashek-seryy);
    transition: all 0.3s ease;
}
.header__network:hover {
    background: rgba(195, 241, 194, 0.6);
}


/* header__bottom */

.header__menu {
    display: contents;
}

.header__nav > ul {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
}
.header__nav > ul > li > a, .header__nav > ul > li > button {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #003649;
    &:hover {
        opacity: 0.5;
    }
}

.header__nav_about > button {
    display: flex;
    align-items: center;
    gap: 10px;
    &::after {
        content: "";
        display: block;
        border-radius: 5px;
        width: 6px;
        height: 6px;
        background: var(--cveta-zelenyy-2);
    }
}

.header__nav_service > button {
    border-radius: 5px;
    padding: 10px;
    background: var(--fon-plashek);
    display: flex;
    align-items: center;
    gap: 10px;
    transition: all 0.3s ease;
    &:hover {
        background: var(--color-zelenyy);
        color: var(--belyy);
        .header__nav_service-span span {
            background: var(--belyy);
        }
    }
}
.header__nav_service-span {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 17px;
    height: 20px;
}
.header__nav_service-span > span {
    border-radius: 5px;
    width: 16px;
    height: 2px;
    background: var(--color-zelenyy);
    transition: all 0.3s ease;
}



/*  header__submenu */
.header__submenu {
    display: none;
    z-index: 100;
}
.hide-item {
    position: relative;
}
.hide-item--active {
    .header__submenu {
        display: block;
    }
}
.hide-item--active.header__nav_about > button {
    opacity: 0.5;
    &::after {
        width: 8px;
        height: 8px;
        opacity: 0.5;
    }
}
.hide-item--active.header__nav_service > button {
    background: var(--plashki);
    color: var(--belyy);
    .header__nav_service-span span {
        background: var(--belyy);
    }
    .header__nav_service-span span:first-child {
        -webkit-transform: rotate(45deg);
                -ms-transform: rotate(45deg);
            transform: rotate(45deg);
        position: relative;
        top: 7px;
    }
    .header__nav_service-span span:nth-child(2) {
        opacity: 0;
    }
    .header__nav_service-span span:last-child {
        -webkit-transform: rotate(-45deg);
                -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
        position: relative;
        bottom: 7px;
    } 
}


/* mobile__wrapper */
.header-mobile__wrapper {
    display: none;
    //width: 100%;
    &-phone {
        order: 0;
    }
}

.header-mobile__burger-btns {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 20px;
}

.header-mobile__btns {
    display: flex;
    gap: 12px;
}

.header-mobile__top_btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    padding: 8px;
    border-radius: 5px;
    background: var(--cveta-salatovyy);
}

.header-mobile__main_btn {
    flex-grow: 1;
    padding: 16px 10px;
}
.header__btn-tg {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    background: #3daede;
    color: var(--tekst-belyy);
}

.header-mobile__eye {
    order: 6;
    text-align: right;
    margin-top: -45px;
    & button {
        transition: all 0.3s ease;
        &:hover {
            opacity: 0.6;
        }
    }
}

.header-mobile__info {
    order: 5;
}



/* burger__menu */
.burger__menu {
    width: 50px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ham {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transition: transform 400ms;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.hamRotate.active {
    transform: rotate(45deg);
}
.hamRotate180.active {
    transform: rotate(180deg);
}
.line {
    fill:none;
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
    stroke:#206175;
    stroke-width:5.5;
    stroke-linecap:round;
}
.ham1 .top {
    stroke-dasharray: 40 139;
}
.ham1 .bottom {
    stroke-dasharray: 40 180;
}
.ham1.active .top {
    stroke-dashoffset: -98px;
}
.ham1.active .bottom {
    stroke-dashoffset: -138px;
}



// media header-1
@media (max-width: 1100px) {
    main {
        margin-top: 165px;
    }
    .header__block {
        padding: 16px 0;
    }

    .header-mobile__wrapper {
        display: block;
    }


    .header__inner {
        display: flex;
        flex-direction: column-reverse;
    }
    .header__info, .header__top_btns, .header__links, .header__search-wrapper  {
        display: none;
    }
    .header__bottom {
        display: contents;
    }


    .header__menu {
        display: none;
    }
    .header__menu--active {
        position: fixed;
        top: 165px;
        right: 0;
        width: 100%;
        z-index: 10;
        height: calc(100vh - 165px);
        overflow-y: auto;
        padding: 10px 20px;
        background-color: #fff;
        display: flex;
        flex-direction: column;
        gap: 30px;
        &::-webkit-scrollbar {
            width: 3px;
        }
        &::-webkit-scrollbar-track {
            background: #DCDCDD;
            border-radius: 3px;
        }
        &::-webkit-scrollbar-thumb {
            border-radius: 3px;
            background: var(--color-temno-zelenyy);
        }

        & .header__top {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            justify-content: flex-start;
            gap: 20px;
            padding: 20px;
        }
        & .header__nav {
            display: block;
            opacity: 1 !important;
            order: 1;
        }
        & .header__btns {
            display: flex;
            order: 2;
            & button {
                flex: 1 0 auto;
            }
        }
        & .header__top_btns {
            display: flex;
            margin-bottom: 30px;
            & button {
                flex-grow: 1;
            }
        }
        & .header__info {
            display: flex;
            flex-direction: column;
            align-items: stretch;
            gap: 22px;
            order: 3;
            padding-bottom: 30px;
            border-bottom: 1px solid rgba(15, 86, 88, 0.2);
            margin-bottom: 30px;
        }
        & .header__links {
            display: flex;
            flex-direction: row-reverse;
            justify-content: space-between;
            order: 4;
        }
    }

    .header__nav {
        display: none;
        & > ul {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-direction: column;
                    flex-direction: column;
            -webkit-box-align: stretch;
                -ms-flex-align: stretch;
                    align-items: stretch;
            gap: 0;
            margin-bottom: 0;
            position: relative;
            & > li > a, & > li > button {
                font-weight: 500;
                padding: 11px 0;
            }
        }
    }
    .header__nav_about {
        & button {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            align-items: center;
            gap: 10px;
            width: 100%;
            font-weight: 500;
        }
    }
    .header__nav_service button {
        width: 100%;
        justify-content: center;
    }
    .header__nav_list {
        position: relative;
        z-index: 10;
    }
}
@media (max-width: 600px) {
    .header__logo p {
        display: none;
    }
    .logo__picture {
        padding-right: 0;
        border-right: none;
    }
}


