// licenses
.licenses-page__title-h1 {
    margin-bottom: 50px;
}

.licenses-page__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    border-bottom: 1px solid rgba(15, 86, 88, 0.2);
    padding-bottom: 50px;
    margin-bottom: 50px;

    & p {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.licenses-page__info-wrap {
    flex-basis: calc(55% - 15px);
}

.licenses-page__container {
    display: flex;
    flex-direction: column;
}

.licenses-page__label {
    flex-basis: calc(45% - 15px);
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 18px;
    line-height: 133%;
    color: var(--cveta-zelenyy);
    padding: 30px;
    border-radius: 10px;
    background: rgba(195, 241, 194, 0.4);
    height: auto;
}

.licenses-page__info-links {
    display: flex;
    gap: 20px;
    justify-content: space-between;
}

.licenses-page__info-link {
    font-size: 14px;
    font-weight: 500;
    color: var(--cveta-zelenyy);
    display: flex;
    align-items: center;
    gap: 8px;
    transition: opacity .3s ease;
    &::after {
        content: '';
        min-width: 14px;
        width: 14px;
        height: 14px;
        background: url("./../img/icons/licenses-download.svg") no-repeat center center / contain;
    }
    &:hover {
        opacity: .6;
    }
}

.licenses-page__info-link__view {
    &::after {
        content: '';
        min-width: 18px;
        width: 18px;
        height: 13px;
        background: url("./../img/icons/licenses-eye.svg") no-repeat center center / contain;
    }
}


// .licenses-page__item.img-animation {
//     border-radius: 10px;
// }

// .licenses-page__item.img-animation:before {
//     border-radius: 10px !important;
// }


.licenses-page__inner {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
}

.licenses-page__picture {
    max-height: 430px;
    border-radius: 10px;

    & img {
        width: 100%;
        height: 100%;
        border-radius: 10px;
        max-height: 430px;
        object-fit: cover;
    }
}


.licenses-page__item-wrap {
    overflow: hidden;
    position: relative;
}

.licenses-page__item {
    display: block;
    border-radius: 30px;
    height: 100%;
    width: 100%;
    max-height: 430px;
}

.licenses-page__item_label {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: var(--fony-plashek-svetlo-seryy);
    padding: 15px;
    border-radius: 10px;
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    color: var(--cveta-zelenyy);
    z-index: 2;
}




// media licenses

@media (max-width: 1000px) {
    .licenses-page__info {
        flex-direction: column;
        gap: 20px;
    }
    .licenses-page__label {
        flex-basis: 100%;
        width: 100%;
    }
    .licenses-page__info-wrap {
        flex-basis: 100%;
    }
    .licenses-page__inner {
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }
}

@media (max-width: 650px) {
    .licenses-page__title-h1 {
        margin-bottom: 30px;
    }
    .licenses-page__info {
        padding-bottom: 20px;
        margin-bottom: 30px;
    }
    .licenses-page__label {
        border-radius: 20px;
        padding: 20px;
        font-size: 16px;
    }
    .licenses-page__info-links {
        flex-direction: column;
        gap: 12px;
        font-size: 14px;
    }
    .licenses-page__inner {
        grid-template-columns: repeat(1, 1fr);
        gap: 10px;
    }
    .licenses-page__item_label {
        padding: 12px;
        font-size: 12px;
    }
    .licenses-page__item {
        border-radius: 10px;
        max-height: 360px;
    }
}