// blockquote-2
.blockquote-2 {
    padding: 25px 0;
    border-top: 1px solid rgba(111, 111, 111, 0.2);
    border-bottom: 1px solid rgba(111, 111, 111, 0.2);
    font-size: 18px;
    line-height: 140%;
    font-style: italic;
    color: var(--cveta-zelenyy);

    &__main {
        display: flex;
        align-items: flex-start;
        gap: 40px;
        margin-bottom: 25px;
    }
    
    &__editor {
        border-radius: 10px;
        padding: 16px 24px;
        background: var(--fony-plashek-svetlo-seryy);
        display: flex;
        align-items: center;
        font-style: normal;
    
        &_left {
            display: flex;
            gap: 20px;
            align-items: center;
            padding-right: 30px;
            border-right: 1px solid var(--izobrazhenie);
            margin-right: 30px;
        }
    
        &_img {
            display: block;
            border-radius: 100px;
            min-width: 60px;
            width: 60px;
            height: 60px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    
        &_text {
            display: flex;
            flex-direction: column;
            gap: 4px;
            & p {
                font-size: 14px;
                line-height: 150%;
                color: var(--fony-plashek-seryy-2);
                margin-bottom: 0 !important;
                &:last-child {
                    font-weight: 500;
                    font-size: 16px;
                    color: var(--tekst-temnyy);
                }
            }
        }

        &_right {
            display: flex;
            flex-direction: column;
            gap: 8px;
        }
    
        &_item {
            display: flex;
            align-items: center;
            gap: 10px;
            font-size: 14px;
            line-height: 150%;
            color: var(--tekst-temnyy);
        }
    }
}


@media (max-width: 688px) {
    .blockquote-2 {
        padding: 27px 0 30px 0;
        margin-top: 15px;
        font-size: 14px;
        font-style: normal;
        position: relative;
        &__main {
            margin-bottom: 20px;
            &_bg {
                position: absolute;
                top: -15px;
                left: 0;
                width: 46px;
                height: 30px;
                padding-right: 16px;
                background-color: #fff;
            }
        }
        &__editor {
            flex-direction: column;
            align-items: stretch;
            &_left {
                padding-right: 0;
                border-right: none;
                margin-right: 0;
                padding-bottom: 16px;
                border-bottom: 1px solid var(--izobrazhenie);
                margin-bottom: 16px;
                gap: 12px;
            }
            &_img {
                min-width: 60px;
                width: 60px;
                height: 60px;
            }
            &_right {
                gap: 4px;
            }
            &_text {
                gap: 2px;
                & p {
                    font-size: 12px;
                    &:last-child {
                        font-size: 14px;
                    }
                }
            }
        }
    }
}
