// tour
.tour__title-h1 {
    margin-bottom: 50px;
}

.tour__inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.tour__item {
    position: relative;
    &:first-child {
        grid-row: span 2;
        grid-column: span 2;
        & .tour__item-descr {
            font-size: 26px;
            bottom: 32px;
            left: 40px;
        }
    }
}

.tour__item.zoom::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    border-radius: 20px;
    opacity: 0.5;
    transition: opacity 0.3s ease;
    z-index: 2;
}

.tour__item.zoom::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60px;
    height: 60px;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    background: url("./../img/help/tour3D.svg") no-repeat center center / cover;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 3;
}

.tour__item.zoom:hover::before {
    opacity: 1;
}
.tour__item.zoom:hover::after {
    opacity: 1;
}


.tour__item-picture {
    border-radius: 20px;
    position: relative;
    overflow: hidden;

    & img {
        border-radius: 20px;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.tour__item-descr {
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 22px;
    line-height: 140%;
    color: var(--tekst-belyy);
    position: absolute;
    bottom: 20px;
    left: 24px;
    right: 24px;
    z-index: 3;
    height: auto;
    transition: opacity 0.3s ease;
}


// media tour
@media (max-width: 1100px) {
    .tour__inner {
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }
    .tour__item {
        position: relative;
        &:first-child {
            grid-row: span 1;
            grid-column: span 1;
            & .tour__item-descr {
                font-size: 22px;
                bottom: 20px;
                left: 24px;
            }
        }
    }
}
@media (max-width: 850px) {
    .tour__inner {
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }
}

@media (max-width: 600px) {
    .tour__title-h1 {
        margin-bottom: 30px;
    }
    .tour__inner {
        grid-template-columns: repeat(1, 1fr);
        gap: 12px;
    }
    .tour__item-descr {
        font-size: 18px;
    }
    .tour__item {
        max-height: 320px;
    }
}