// about-1
.about {
    &__grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
        margin-bottom: 40px;
    }

    &__item {
        position: relative;
    }

    &__title {
        color: var(--tekst-temnyy);
        margin-bottom: 40px;
    }

    &__text p:not(:last-child){
        margin-bottom: 20px;
    }

    &__img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 30px;
        overflow: hidden;
    }

    &__advantages {
        display: flex;
        gap: 20px;
    }

    &__advantage {
        flex: 1 0 20%;
        border: 1px solid rgba(111, 111, 111, 0.3);
        border-radius: 30px;
        padding: 25px;
        display: flex;
        flex-direction: column;
        gap: 10px;

        & p:first-child {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 18px;
            line-height: 133%;
            color: var(--fony-plashek-seryy-2);
        }
        & span {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 44px;
            line-height: 110%;
            color: var(--cveta-zelenyy);
        }
        & p:last-child {
            font-size: 14px;
        }
    }
}


// media about-1
@media (max-width: 850px) {
    .about {
        &__grid {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
            margin-bottom: 20px;
        }
        &__title {
            margin-bottom: 20px;
        }
        &__text p:not(:last-child){
            margin-bottom: 14px;
        }
        &__img img {
            max-height: 340px;
            border-radius: 20px;
        }
        &__advantages {
            flex-direction: column;
            gap: 10px;
        }
        &__advantage {
            flex: 1 0 20%;
            border-radius: 14px;
            padding: 20px;
            gap: 6px;
            & p:first-child {
                font-size: 16px;
            }
            & span {
                font-size: 28px;
            }
        }
    }
}
