// legal-docs-3
.legal-docs {
    &__title-h1 {
        margin-bottom: 50px;
    }

    &__inner {
        display: flex;
        gap: 40px;
    }

    &__left {
        flex: 1 0 26%;
        & p {
            margin-bottom: 20px;
        }
        & a {
            display: block;
            width: fit-content;
        }
    }

    &__list {
        flex: 1 0 60%;
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__item {
        border-radius: 20px;
        padding: 20px 30px;
        background: var(--fony-plashek-svetlo-seryy);
        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            margin-bottom: 12px;
            & p {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 18px;
                line-height: 133%;
                color: var(--cveta-zelenyy);
            }
        }

        &_link {
            display: flex;
            align-items: center;
            gap: 10px;
            flex: 0 0 auto;
            padding: 10px 18px;
            background: rgba(195, 241, 194, 0.4);
            &::before {
                content: "";
                display: block;
                background-image: url("./../img/icons/licenses-download.svg");
                background-size: cover;
                min-width: 14px;
                width: 14px;
                height: 14px;
                order: 2;
            }
        }

        &_info {
            display: flex;
            gap: 100px;
            font-size: 14px;
            & span {
                font-weight: 500;
                color: var(--cveta-zelenyy);
            }
        }
    }
}



// media legal-docs-3
@media (max-width: 1000px) {
    .legal-docs {
        &__inner {
            flex-direction: column;
            gap: 30px;
        }
    }
}
@media (max-width: 688px) {
    .legal-docs {
        &__title-h1 {
            margin-bottom: 30px;
        }
        &__left {
            & a {
                width: 100%;
            }
        }
        &__item {
            padding: 18px 20px;
            &_top {
                flex-direction: column;
                align-items: stretch;
                gap: 16px;
                margin-bottom: 32px;
                & p {
                    font-size: 18px;
                }
            }
            &_link {
                justify-content: center;
                flex: 1 0 auto;
            }
            &_info {
                flex-direction: column;
                gap: 8px;
            }
        }
    }
}