// contacts-2
.contacts {
    position: relative;

    &__wrapper {
        padding: 100px 0;
        display: flex;
        justify-content: flex-end;
    }

    &__contant {
        border: 1px solid rgba(107, 107, 107, 0.2);
        border-radius: 30px;
        padding: 30px;
        box-shadow: 0 0 16px 0 rgba(0, 0, 0, 0.11);
        background: var(--tekst-belyy);
        position: relative;
        z-index: 3;
    }

    &__title {
        color: var(--cveta-zelenyy);
        margin-bottom: 30px;
    }

    &__inner {
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-bottom: 30px;
        flex-basis: 40%;
    }

    &__item {
        display: flex;
        flex-direction: column;
        gap: 6px;
        & a:hover, & button:hover {
            text-decoration: underline;
        }
    }

    &__phone {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 26px;
        line-height: 140%;
        color: var(--cveta-zelenyy);
    }

    &__location {
        font-size: 18px;
        font-weight: 400;
        color: var(--cveta-zelenyy);
        & button {
            font-weight: 500;
        }
    }

    &__text {
        font-weight: 500;
        font-size: 18px;
        line-height: 140%;
        color: var(--cveta-zelenyy);
    }

    &__networks {
        display: flex;
        gap: 6px;
    }

    &__network {
        border-radius: 5px;
        padding: 10px;
        width: 40px;
        height: 40px;
        background: rgba(7, 57, 58, 0.5);
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.3s ease;
        &:hover {
            background: rgba(7, 57, 58, 0.7);
        }
    }

    &__license {
        border-radius: 20px;
        padding: 20px;
        background: rgba(195, 241, 194, 0.6);
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        color: var(--cveta-zelenyy);
        display: flex;
        flex-direction: column;
        gap: 5px;
        & a {
            text-decoration: underline;
            color: var(--cveta-zelenyy);
        }
    }

    &__btns {
        display: flex;
        gap: 10px;
        & button {
            flex: 1 0 40%;
            padding: 13px 20px;
        }
    }

    &__maps {
        width: 100%;
        height: 100%;
        min-height: 666px;
        position: absolute;
        overflow: hidden;
        flex-basis: 60%;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }
}

.contacts-page {
    &__wrapper {
        padding: 0 0 100px;
        justify-content: flex-start;
    }
}



// media contacts-2
@media (max-width: 688px) {
    .contacts {
        &__wrapper {
            padding: 358px 0 60px;
            & > * {
                width: 100%;
            }
        }
        &__contant {
            border-radius: 20px;
            padding: 20px;
        }
        &__title {
            margin-bottom: 20px;
        }
        &__inner {
            gap: 20px;
            margin-bottom: 20px;
        }
        &__phone {
            font-size: 20px;
        }
        &__text, &__location {
            font-size: 16px;
        }
        &__license {
            border-radius: 10px;
            padding: 14px;
            font-size: 12px;
        }
        &__btns {
            flex-direction: column;
        }
        & img {
            object-position: 30% 0%;
        }
    }
    .contacts-page {
        &__wrapper {
            padding: 280px 0 60px;
        }
    }
}