// rating-1
.rating {
    padding: 80px 0 100px;
    background: var(--cveta-zelenyy);

    &__top {
        margin-bottom: 50px;
    }

    &__title {
        color: var(--cveta-salatovyy);
    }

    &__cards {
        display: flex;
        gap: 20px;
    }

    &__card {
        flex: 1 0 30%;
        border: 1px solid var(--cveta-salatovyy);
        border-radius: 20px;
        padding: 30px;
        color: var(--tekst-belyy);

        &_name {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 10px;
            margin-bottom: 20px;
        }

        &_estimation {
            display: flex;
            align-items: center;
            gap: 10px;
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
        }

        &_stars {
            display: flex;
            gap: 4px;
        }
        &_star-active {
            & svg path {
                fill: var(--cveta-salatovyy);
            }
        }

        &_info {
            font-weight: 400;
            font-size: 12px;
            opacity: 0.6;
            display: flex;
            justify-content: space-between;
            gap: 10px;
            & p:last-child {
                text-align: right;
            }
        }

        &_line {
            width: 1px;
            min-height: 100%;
            background-color: rgba(255, 255, 255, 0.2);
        }
    }
}


// media rating-1
@media (max-width: 1100px) {
    .rating {
        &__cards {
            flex-direction: column;
            gap: 20px;
        }
    }
}
@media (max-width: 600px) {
    .rating {
        padding: 40px 0 60px;
        &__top {
            margin-bottom: 30px;
        }
        &__card {
            border-radius: 14px;
            padding: 20px;
            &_logo img {
                max-height: 26px;
            }
            &_estimation {
                font-size: 20px;
            }
            &_star, &_star-active {
                width: 16px;
                height: 16px;
            }
            &_info {
                font-size: 10px;
            }
        }
    }
}