// services-7
.services {
    padding: 100px 0;
    background: url("./../img/services/services-bg.jpg");

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 50px;
    }

    &__title {
        color: var(--tekst-belyy);
    }

    &__btn {
        color: var(--cveta-salatovyy);
        &::after {
            border-top: 1px solid var(--cveta-salatovyy);
            border-right: 1px solid var(--cveta-salatovyy);
        }
    }

    &__row {
        display: flex;
        gap: 20px;
    }

    &__tab {

        &_btns {
            border: 1px solid #82B9A8;
            border-radius: 30px;
            padding: 6px;
            backdrop-filter: blur(14px);
            background: rgba(73, 73, 73, 0.1);
            flex-basis: 46%;
        }

        &_btn {
            display: block;
            border-radius: 40px;
            padding: 18px 20px;
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst-belyy);
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 16px;
            & span {
                flex: 1 0 auto;
                text-align: left;
            }
            &:hover {
                background: rgba(7, 57, 58, 0.4);
            }
            &.tab__btn--active {
                background: rgba(7, 57, 58, 0.6);
            }
        }
    }

    &__tabcontent {
        min-height: 100%;
        overflow: hidden;
        transition: all 0.3s ease;
    }

    &__card {
        border-radius: 30px;
        padding: 10px;
        backdrop-filter: blur(36.099998474121094px);
        background: rgba(7, 57, 58, 0.6);
        width: 100%;
        min-height: 100%;
        &_top {
            border-radius: 30px;
            padding: 16px 30px;
            background: var(--tekst-belyy);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
        }
    }

    &__name {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 26px;
        line-height: 140%;
        color: var(--cveta-zelenyy);
    }

    &__quantity {
        font-size: 14px;
        color: var(--cveta-zelenyy);
    }

    &__links {
        padding: 30px;
        display: flex;
        height: 80%;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        & ul {
            display: flex;
            flex-wrap: wrap;
            & li {
                flex: 1 0 43%;
                max-width: 50%;
                padding-bottom: 20px;
                border-bottom:  1px solid rgba(255, 255, 255, 0.2);
                margin-bottom: 20px;
                &:nth-child(2n-1) a {
                    padding-right: 30px;
                    border-right: 1px solid rgba(255, 255, 255, 0.2);
                }
                &:nth-child(2n) a {
                    margin-left: 30px;
                }
            }
            & a {
                font-weight: 400;
                color: var(--tekst-belyy);
                display: flex;
                justify-content: space-between;
                align-items: center;
                &::after {
                    border-top: 1px solid var(--tekst-belyy);
                    border-right: 1px solid var(--tekst-belyy);
                }
            }
        }
    }

    &__btn-more {
        color: var(--cveta-salatovyy);
        &::after {
            border-top: 1px solid var(--cveta-salatovyy);
            border-right: 1px solid var(--cveta-salatovyy);
        }
    }
}




// media services-7
@media (max-width: 1070px) {
    .services {
        &__row {
            flex-direction: column;
            gap: 0;
            border-radius: 20px;
            padding: 10px;
            backdrop-filter: blur(12px);
            background: rgba(73, 73, 73, 0.1);
            border: 1px solid #82B9A8;
        }
        &__tab {
            &_btns {
                display: contents;
            }
            &_btn {
                border-radius: 16px;
            }
        }
        &__card {
            border-radius: 16px;
            padding: 20px 16px;
            background: var(--tekst-belyy);
            width: 100%;
            height: 100%;
            &_top {
                display: none;
            }
        }
        &__links {
            & ul {
                & li {
                    flex: 1 0 100%;
                    border-bottom:  1px solid rgba(0, 0, 0, 0.2);
                    margin-bottom: 20px;
                    &:nth-child(2n-1) a {
                        padding-right: 30px;
                        border-right: 1px solid rgba(0, 0, 0, 0.2);
                    }
                }
                & a {
                    font-weight: 300;
                    color: var(--tekst-svetlyy);
                    &::after {
                        border-top: 1px solid var(--cveta-zelenyy);
                        border-right: 1px solid var(--cveta-zelenyy);
                    }
                }
            }
        }
        &__btn-more {
            border: 1px solid var(--cveta-zelenyy);
            border-radius: 16px;
            padding: 10px 20px;
            text-align: center;
            color: var(--cveta-zelenyy);
            &::after {
                border-top: 1px solid var(--cveta-zelenyy);
                border-right: 1px solid var(--cveta-zelenyy);
            }
        }
    }
}
@media (max-width: 800px) {
    .services {
        &__flex {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
        &__top {
            display: contents;
        }
        &__btn {
            display: block;
            order: 3; 
            width: 100%; 
        }
        &__cards {
            order: 2;
            gap: 10px;
            margin-bottom: 10px;
        }
        &__tabcontent {
            width: 100%;
        }
    }
}

@media (max-width: 688px) {
    .services {
        &__tab {
            &_btn {
                padding: 16px 20px;
                font-size: 16px;
            }
        }
        &__card {
            display: flex;
            flex-direction: column;
            border-radius: 16px;
            padding: 20px;
            margin: 10px 0;
            &_top {
                display: contents;
            }
        }
        &__name {
            display: none;
        }
        &__quantity {
            order: 2;
            margin-bottom: 10px;
        }
        &__links {
            display: contents;
            & ul {
                display: flex;
                flex-direction: column;
                margin-bottom: 24px;
                & li {
                    padding-bottom: 14px;
                    margin-bottom: 14px;
                    max-width: 100%;
                    &:last-child {
                        padding-bottom: 0;
                        border-bottom: none;
                        margin-bottom: 0;
                    }
                    &:nth-child(n) a {
                        padding-right: 0;
                        border-right: none;
                        margin: 0;
                    }
                }
            }
        }
        &__btn-more {
            order: 3;
            border: 1px solid var(--cveta-zelenyy);
            border-radius: 16px;
            padding: 10px 20px;
            text-align: center;
            width: 100%;
        }
    }
    
}