// advantages-2
.advantages {
    padding: 100px 0;
    background: var(--cveta-zelenyy);
    
    &__title {
        color: var(--cveta-salatovyy);
    }

    &__cards {
        display: flex;
        flex-wrap: wrap;
    }

    &__card {
        flex: 1 0 30%;
        padding: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 40px;
        font-weight: 400;
        color: var(--tekst-belyy);

        &_top {
            display: flex;
            align-items: center;
            gap: 10px;
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 22px;
            line-height: 140%;
            color: var(--cveta-salatovyy);
        }

        &:nth-child(2), &:nth-child(4n), &:nth-child(4n + 1) {
            border-right: 1px solid rgba(255, 255, 255, 0.2);
        }
        &:nth-child(-n + 3) {
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        }
        &:last-child {
            border-right: none;
        }
    }
}


// media advantages-2
@media (max-width: 1050px) {
    .advantages {
        padding: 80px 0;
        &__card {
            flex: 1 0 45%;
            &:nth-child(2), &:nth-child(4n), &:nth-child(4n + 1) {
                border-right: none;
            }
            &:nth-child(2n-1) {
                border-right: 1px solid rgba(255, 255, 255, 0.2);
            }
            &:nth-child(-n + 4) {
                border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            }
        }
    }
}
@media (max-width: 650px) {
    .advantages {
        padding: 40px 0;
        &__cards {
            gap: 0;
            margin-bottom: 10px;
        }
        &__card {
            flex: 1 0 100%;
            padding: 20px 0;
            gap: 12px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            &_top {
                font-size: 18px;
            }
            & img {
                width: 20px;
                height: 20px;
            }
            &:nth-child(2n-1) {
                border-right: none;
            }
        }
    }
}