// error-page-2
.error-page {
    padding: 20px 0 80px;
    position: relative;

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__inner {
        max-width: 714px;
        margin: 0 auto;
    }

    &__number {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 240px;
        line-height: 80%;
        color: var(--cveta-salatovyy);
        text-align: center;
        margin-bottom: 50px;
    }

    &__text {
        line-height: 140%;
        text-align: center;
        color: var(--tekst-belyy);
        margin-bottom: 40px;
        & a {
            color: var(--tekst-belyy);
            font-weight: 500;
            text-wrap: nowrap;
            text-decoration: underline;
        }
    }

    &__links {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
    }

    &__link {
        width: calc(50% - 5px);
        border-radius: 40px;
        padding: 10px 10px 10px 20px;
        background: var(--cveta-zelenyy);
        font-weight: 500;
        line-height: 140%;
        color: var(--cveta-salatovyy);
        display: flex;
        align-items: center;
        justify-content: space-between;
        transition: all 0.3s ease;
        &:hover {
            opacity: 0.7;
        }
    }
}

// media error-page-2
@media (max-width: 688px) {
    .error-page {
        padding: 40px 0;
        &__number {
            font-size: 140px;
            margin-bottom: 30px;
        }
        &__text {
            text-align: left;
            margin-bottom: 30px;
        }
        &__link {
            width: 100%;
        }
    }
}