// page-articles-1
.page-articles {
    &__title-h1 {
        margin-bottom: 50px;
    }

    &__left {
        display: block;
    }
    &__title {
        margin-bottom: 30px;
    }

    &__grid {
        display: grid;
        grid-template-columns: 3.4fr 6.8fr;
        gap: 60px;
    }

    &__sort {
        padding-top: 26px;
        border-top: 1px solid rgba(7, 57, 58, 0.2);

        &_text {
            margin-bottom: 18px;
        }

        &_item {
            margin-bottom: 12px;
            display: flex;
            gap: 12px;
            align-items: center;

            & input[type="checkbox"] {
                position: relative;
                width: 28px;
                height: 28px;
                color: #000;
                border: 1px solid #788189;
                border-radius: 5px;
                appearance: none;
                outline: 0;
                cursor: pointer;
                transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
                &::before {
                    position: absolute;
                    content: '';
                    display: block;
                    top: 3px;
                    left: 9px;
                    width: 8px;
                    height: 14px;
                    border-style: solid;
                    border-color: #fff;
                    border-width: 0 3px 3px 0;
                    transform: rotate(45deg);
                    opacity: 0;
                }
                &:checked {
                    color: #fff;
                    border-color: var(--cveta-zelenyy-2);
                    background: var(--cveta-zelenyy-2);
                    &::before {
                        opacity: 1;
                    }
                }
            }
            & label {
                position: relative;
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                color: var(--tekst-svetlyy);
                &::before {
                    position: absolute;
                    content: attr(data-content);
                }
            }
        }

        &_quantity {
            font-size: 14px;
            color: var(--tekst-svetlyy);
        }
    }

    &__card {
        padding-top: 20px;
        border-top: 1px solid rgba(7, 57, 58, 0.2);
        &:not(:last-child){
            margin-bottom: 40px;
        }

        &_top {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;
            margin-bottom: 12px;
        }

        &_row {
            display: flex;
            gap: 30px;
        }

        &_info {
            flex: 1 0 60%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 20px;
        }

        &_img {
            flex: 1 0 30%;
            display: block;
            min-height: 100%;
            max-width: 33%;
            width: 100%;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 20px;
            }
        }

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst-temnyy);
        }

        &_date {
            font-size: 14px;
        }

        &_labels {
            display: flex;
            gap: 6px;
            flex-wrap: wrap;
        }

        &_label {
            border-radius: 40px;
            padding: 8px 16px;
            background: rgba(195, 241, 194, 0.4);
            font-weight: 500;
            font-size: 14px;
            color: rgba(7, 57, 58, 0.8);
        }
    }
}


// media page-articles-1
@media (max-width: 980px) {
    .page-articles {
        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 20px;
        }
        &__left {
            display: flex;
            margin-bottom: 0;
        }
        &__title {
            margin-bottom: 0;
        }
    }
}
@media (max-width: 800px) {
    .page-articles {
        &__left {
            display: contents;
            margin-bottom: 0;
        }
    }
}

@media (max-width: 688px) {
    .page-articles {
        &__title-h1 {
            margin-bottom: 30px;
        }
        &__sort {
            &_text {
                margin-bottom: 18px;
            }
            &_item {
                margin-bottom: 14px;
                & input[type="checkbox"] {
                    width: 22px;
                    height: 22px;
                    &::before {
                        top: 3px;
                        left: 7px;
                        width: 6px;
                        height: 10px;
                        border-width: 0 2px 2px 0;
                    }
                }
                & label {
                    font-size: 14px;
                }
            }
        }

        &__card {
            padding-top: 14px;
            &:not(:last-child){
                margin-bottom: 30px;
            }
            &_top {
                flex-direction: column-reverse;
                align-items: flex-start;
                gap: 20px;
                margin-bottom: 14px;
            }
            &_row {
                flex-direction: column;
                gap: 20px;
            }
            &_img {
                max-width: 100%;
                max-height: 200px;
                & img {
                    max-height: 200px;
                }
            }
            &_title {
                font-size: 18px;
                color: var(--cveta-zelenyy);
            }
            &_date {
                font-size: 12px;
            }
        }
    }
}
