// calculator-4
.calculator {
    &__title {
        margin-bottom: 30px;
        &-mobile {
            display: none;
        }
    }

    &__btn-more {
        display: none;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 60px;
    }

    &__left {
        flex: 1 0 30%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
    }

    &__right  {
        flex: 1 0 60%;
        border-radius: 15px;
        padding: 60px 50px;
        background: var(--fony-plashek-svetlo-seryy);
        display: flex;
        flex-wrap: wrap;
        gap: 40px 20px;
    }

    &__select {
        width: calc(50% - 10px);
        & select {
            background: var(--belyy) url(./../img/icons/arrow-bottom.svg) no-repeat right;
            background-position-x: calc(100% - 8px);
            border-radius: 5px;
            min-height: 100%;
            border-color: transparent;
        }
    }

    &__input {
        width: 100%;
        border-radius: 5px;
        padding: 12px 20px;
        padding-top: 32px;
        padding-right: 30px;
    }

    &__slider {
        width: calc(50% - 10px);
        font-weight: 400;
        color: var(--tekst-temnyy);
        & label {
            display: block;
            margin-bottom: 10px;
        }
        &_output {
            display: block;
            border-radius: 8px;
            padding: 18px 20px;
            background: var(--belyy);
            color: var(--tekst);
        }
        &_container {
            width: 100%; 
            margin-top: -11px;
        }
        &_values {
            display: flex;
            justify-content: space-between;
            gap: 10px;
            color: var(--tekst);
        }
        &_values-time {
            & p:not(:first-child, :last-child) {
                flex: 1 0 20%;
                text-align: center;
            }
        }
    }

    &__result {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        font-weight: 500;
        padding-top: 20px;
        border-top: 1px solid rgba(15, 86, 88, 0.2);
        & span {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 140%;
            color: var(--cveta-zelenyy);
        }
    }
}
.calculator-content {
    display: contents;
}



// media calculator-4
@media (max-width: 1160px) {
    .calculator {
        &__row {
            flex-direction: column;
            gap: 20px;
        }
        &__left {
            display: contents;
        }
        &__btn {
            order: 3;
            width: fit-content;
            margin: 0 auto;
        }
    }
}
@media (max-width: 688px) {
    .calculator {
        &__row {
            flex-direction: column;
            gap: 0;
        }
        &__text {
            padding: 16px 20px;
            border-radius: 16px;
            background: var(--fony-plashek-svetlo-seryy);
            margin-bottom: 20px;
        }
        &__title {
            margin-bottom: 20px;
            display: none;
            &-mobile {
                display: block;
            }
        }
        &__btn-more {
            display: flex;
            justify-content: center;
            width: 100%;
            margin-top: 20px;
            &::after {
                content: "";
                position: static;
                opacity: 1;
                background-color: transparent;
                display: inline-block;
                width: 8px;
                height: 8px;
                border-top: 1px solid var(--cveta-zelenyy);
                border-right: 1px solid var(--cveta-zelenyy);
                transform: rotate(135deg);
                margin-top: 4px;
                margin-left: 8px;
                transition: all 0.3s ease;
            }
        }
        &__right {
            border-radius: 10px;
            padding: 20px;
            flex-direction: column;
            flex-wrap: nowrap;
            gap: 20px;
        }
        &__select, &__slider, &__btn {
            width: 100%;
        }
        &__result {
            & span {
                font-size: 18px;
            }
        }
    }
    .calculator-content {
        display: block;
        max-height: 0;
        overflow: hidden;
        transition: all 0.3s ease;
        &.active {
            margin-bottom: 20px;
        }
    }
}
@media (max-width: 380px) {
    .calculator {
        &__slider {
            &_values-time {
                & p:not(:first-child, :last-child) {
                    flex: 1 0 17%;
                }
            }
        }
    }
}
