// articles-3
.articles {
    overflow: hidden;

    &__inner {
        position: relative;
    }

    &__btns {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;
    }

    &__swiper-btns {
        position: absolute;
        top: 50%;
        left: -80px;
        transform: translate(0, calc(-50% - 40px));
        width: calc(100% + 160px);
        justify-content: space-between;
        z-index: 10;
    }

    &__swiper-slide {
        height: auto;
    }

    &__cards {
        margin-bottom: 40px;
    }

    &__card {
        display: block;
        height: 100%;
        border-radius: 20px;
        background: var(--fony-plashek-svetlo-seryy);
        position: relative;

        &_picture {
            display: block;
            border-radius: 10px;
            overflow: hidden;
            position: relative;
            width: 100%;
            height: 240px;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_labels {
            display: flex;
            gap: 6px;
            position: absolute;
            top: 10px;
            left: 10px;
            right: 10px;
            & p {
                border-radius: 10px;
                padding: 8px 16px;
                background: var(--tekst-belyy);
                font-weight: 500;
                font-size: 14px;
                color: var(--cveta-zelenyy);
            }
        }

        &_inner {
            border-radius: 10px;
            padding: 30px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 40px;
            height: calc(100% - 240px);
        }

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 14px;
        }

        &_bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
        }

        &_date {
            font-weight: 400;
            font-size: 14px;
        }
    }
}


// media articles-3
@media (max-width: 1500px) {
    .articles {
        &__btns {
            display: flex;
            align-items: center;
            justify-content: center;
        }
        &__swiper-btns {
            position: static;
            display: contents;
        }
        &__swiper-button-next {
            order: 3;
        }
    }
}
@media (max-width: 800px) {
    .articles {
        &__cards {
            margin-bottom: 20px;
        }
        &__inner {
            margin-bottom: 10px;
        }
        &__swiper-btns {
            display: none;
        }
    }
}
@media (max-width: 688px) {
    .articles {
        &__card {
            border-radius: 10px;
            &_picture {
                height: 200px;
            }
            &_labels {
                flex-wrap: wrap;
                & p {
                    padding: 6px 10px;
                    font-size: 12px;
                }
            }
            &_inner {
                padding: 20px;
                height: calc(100% - 200px);
            }
            &_title {
                font-size: 20px;
            }
            &_date {
                font-size: 12px;
                padding: 8px 12px;
            }
        }
    }
}


