// popup cokie
.popup-widget {
    position: fixed;
    right: 40px;
    bottom: 40px;
    z-index: 20;


    &__inner, &__row {
        display: flex;
        gap: 15px;
        position: relative;
    }

    &__item {
        padding: 8px 20px;
        border-radius: 80px;
        background: #22787b;
        font-weight: 600;
        font-size: 14px;
        line-height: 130%;
        text-transform: uppercase;
        text-align: left;
        color: var(--tekst-belyy);
        display: flex;
        align-items: center;
        gap: 10px;
        transition: all 0.3s ease;
        &:last-child {
            padding: 17px 16px 16px 14px;
        }
        &:hover {
            background: var(--cveta-zelenyy);
        }
    }

    &__close {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 54px;
        height: 54px;
        border-radius: 80px;
        background: var(--tekst-belyy);
        border: 3px solid #f1f8fd;
    }
}

// media popup cokie
@media (max-width: 970px) {
    .popup-widget {
        right: 10px;
        bottom: 10px;
    }
}
@media (max-width: 688px) {
    .popup-widget {
        position: fixed;
        right: 0;
        bottom: 0;
        left: 0;
        padding: 12px 20px;
        border-radius: 16px 16px 0 0;
        background: #22787b;

        &__inner {
            display: contents;
        }
    
        &__row {
            display: flex;
            gap: 12px;
        }
    
        &__item {
            padding: 8px 0;
            border-radius: 0;
            background: transparent;
            padding-right: 12px;
            border-right: 1px solid rgba(255, 255, 255, 0.2);
            font-size: 11px;
            gap: 5px;
            flex-grow: 1;
            justify-content: center;
            &:last-child {
                padding: 0;
                border-right: none;
                order: 3;
            }
            &:hover {
                background: transparent;
            }
        }
        &__btn {
            position: absolute;
            top: -18px;
            right: 4px;
        }
        &__close {
            width: 32px;
            height: 32px;
            border: 2px solid #22787b;
        }
    }
}
@media (max-width: 500px) {
    .popup-widget {
        &__item-phone {
            order: 2;
            & span {
                display: none;
            }
        }
    }
}