// page-faq-1
.page-faq {
    &__title {
        margin-bottom: 50px;
    }

    &__grid {
        display: grid;
        grid-template-columns: 4fr 8.4fr;
        gap: 60px;
    }

    &__sort {
        display: flex;
        flex-direction: column;
        gap: 20px;
        margin-bottom: 60px;
    }

    &__list {
        list-style-type: none;
        counter-reset: item;
    }

    &__item {
        display: flex;
        gap: 30px;
        &::before{
            content: "("counter(item, decimal-leading-zero)")";
            counter-increment: item;
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 34px;
            line-height: 140%;
            color: var(--cveta-salatovyy);
            margin-top: 20px;
        }
    }

    &__btn-more {
        display: none;
    }
}


// media page-faq-1
@media (max-width: 960px) {
    .page-faq {
        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 40px;
        }
        &__title {
            margin-bottom: 30px;
        }
        &__sort {
            gap: 14px;
            margin-bottom: 26px;
        }
    }
}
@media (max-width: 800px) {
    .page-faq {
        &__form {
            display: block;
            & .faq__form {
                border-radius: 20px;
                padding: 20px;
                &_title {
                    font-size: 18px;
                }
            }
        }
    }
}
@media (max-width: 688px) {
    .page-faq {
        &__item {
            display: flex;
            gap: 16px;
            &::before{
                font-size: 18px;
                margin-top: 20px;
            }
        }
        &__btn-more {
            display: flex;
        }
    }
}
