.popup-feedback {
    &__content {
        background: transparent;
        display: flex;
        max-width: 1200px;
        padding: 60px;
        border-radius: 30px;
        box-shadow: 0 0 30px 0 rgba(255, 255, 255, 0.1);
        background: var(--tekst-belyy);
    }

    &__left {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-right: 40px;
        border-right: 1px solid rgba(15, 86, 88, 0.2);
        flex: 1 0 40%;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 34px;
        line-height: 140%;
        color: var(--tekst-temnyy);
        margin-bottom: 50px;
    }

    &__subtitle {
        margin-bottom: 40px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 15px
    }

    &__input {
        border: 1px solid rgba(84, 84, 84, 0.2);
    }
}


.popup-feedback__inner {
    display: flex;
    gap: 40px;
    align-items: stretch;
}

.popup-feedback__wrap {
    display: flex;
    flex-direction: column;
    flex: 1 0 50%;
}

.popup-feedback__bottom-wrap {
    display: flex;
    align-items: center;
    gap: 15px;
    padding-top: 15px;
    border-top: 1px solid var(--color-svetlo-zelenyy);
}

.popup-feedback__picture {
    height: 100%;

    & img {
        max-height: 390px;
        height: 100%;
        width: 100%;
        border-radius: 20px;
        object-fit: cover;
    }
}

.popup-feedback__stars {
    display: flex;
    align-items: center;
    gap: 15px;
    justify-content: space-between;
    border-radius: 40px;
    padding: 10px 20px;
    background: var(--fony-plashek-svetlo-seryy);
    & p {
        font-weight: 500;
        font-size: 14px;
        color: var(--cveta-zelenyy);
    }
}

.popup-feedback__textarea {
    border: 1px solid rgba(84, 84, 84, 0.2);
    resize: none;
    height: 140px;
}

.popup-feedback__form-input__wrap {
    display: flex;
    gap: 15px;
    font-size: 14px;

    &>* {
        flex-basis: calc(50% - 7.5px);
    }
}

.popup-feedback__politic {
    color: var(--cveta-zelenyy);
}



.popup-feedback-response {
    &__content {
        max-width: 606px;
        padding: 10px;
        display: flex;
        flex-direction: column;
    }

    &__top {
        border-radius: 10px;
        padding: 30px 10px;
        background: var(--belyy);
        text-align: center;
        margin-bottom: 30px;

        & p {
            margin: 0;
        }
    }

    &__inner {
        padding: 0 30px 30px 30px;
    }

    &__main {
        padding: 0 15px;
        padding-bottom: 30px;
        border-bottom: 1px solid var(--color-svetlo-zelenyy);
        margin-bottom: 30px;
        text-align: center;

        & img {
            margin: 0 auto;
            margin-bottom: 20px;
        }

        & p {
            font-size: 16px;
            color: var(--tekst-svetlyy);
        }

        &_error {
            padding-bottom: 0;
            border-bottom: none;
            margin-bottom: 0;
        }
    }

    &__btn {
        display: block;
        width: 100%;
    }
}





@media (max-width: 924px) {
    .popup-feedback__inner {
        flex-direction: column;
        gap: 30px;
        width: 100%;
    }
    .popup-feedback__left {
        padding-right: 0;
        border-right: none;
        flex: 1 0 auto;
    }
    .popup-feedback__title {
        font-size: 22px;
        margin-bottom: 12px;
    }
    .popup-feedback__subtitle {
        margin-bottom: 20px;
    }
    .popup-feedback__wrap {
        flex: 1 0 auto;
    }
    .popup-feedback__picture {
        max-height: 320px;
        width: 100%;
        & img {
            object-position: top;
        }
    }
}

@media (max-width: 650px) {
    .popup-feedback__content {
        border-radius: 20px;
        padding: 30px 20px 20px 20px;
    }
    .popup-feedback__form-input__wrap {
        flex-direction: column;
        &>* {
            flex-basis: auto;
        }
    }
    .popup-feedback__bottom-wrap {
        flex-direction: column;
    }
    .popup-feedback__picture {
        max-height: 250px;
    }
    .popup-feedback__form-input__wrap,
    .popup-feedback__form,
    .popup-feedback__bottom-wrap {
        gap: 10px;
    }
    .popup-feedback__stars {
        padding: 10px;
        flex-wrap: wrap;
        align-items: flex-start;
        gap: 10px;
        background-color: transparent;
    }
    .popup-feedback__close {
        top: 5px;
        right: 5px;
    }
}
