// popup-city-2

.popup-city {
    &__content {
        border-radius: 20px;
        padding: 70px 50px;
        background: var(--belyy);
        max-width: 500px;
        min-height: 738px;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 34px;
        line-height: 140%;
        text-align: center;
        color: var(--tekst-temnyy);
        margin-bottom: 20px;
    }

    &__search {
        max-height: 430px;
        overflow-y: auto;
        &-result {
            max-height: 430px;
        }
    }

    &__list {
        & li a {
            display: block;
            border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
            padding: 14px 0;
            margin-right: 20px;
            font-size: 16px;
            color: var(--tekst-svetlyy);
            transition: all 0.3s ease;
            &:hover {
                color: var(--cveta-zelenyy);
            }
        }

        &_city-active {
            display: flex !important;
            align-items: center;
            gap: 12px;
            font-weight: 500;
            color: var(--tekst-temnyy) !important;
            &::before {
                content: url("./../img/icons/location-2-active.svg");
            }
        }
    }

    &__list_result {
        & li {
            border-bottom: 1px solid var(--color-svetlo-zelenyy-2);
            & a {
               display: block;
               border-radius: 8px;
               padding: 14px 20px 14px 16px;
               margin: 0 4px;
               font-size: 16px;
               color: var(--tekst-temnyy);
               &:hover {
                   background-color: var(--cveta-salatovyy);
               }
               & span:first-child {
                   display: flex; 
                   align-items: center;
                   gap: 10px;
                   &::before {
                       content: url("./../img/icons/location-2.svg");
                   }
               }
               & span:last-child {
                   font-size: 14px;
                   color: var(--tekst-svetlyy);
               }
           }
        }
    }

    &__no-results-message {
        &::before {
            content: url("./../img/icons/no-location-2.svg");
        }
    }

    &__close {
        top: 20px;
        right: 20px;
    }
}



// media popup-city-2
@media (max-width: 600px) {
    .popup-city {
        &__content {
            border-radius: 20px;
            padding: 20px;
            min-height: 608px;
        }
        &__title {
            font-size: 24px;
            text-align: left;
        }
        &__search {
            max-height: 408px;
            &-result {
                max-height: 444px;
            }
        }
        &__list {
            & li a {
                font-size: 14px;
            }
        }
        &__list_result li a {
                & span:last-child {
                    font-size: 12px;
                }
        }
    }
}

