// sub-stock-3
.substock {
    &__row {
        display: flex;
        gap: 20px;
    }

    &__between {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        gap: 20px;
    }

    &__item {
        flex: 1 0 30%;
        border: 1px solid rgba(111, 111, 111, 0.3);
        border-radius: 20px;
        min-height: 202px;
        overflow: hidden;
        position: relative;
        padding: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        &:first-child {
            background: rgba(195, 241, 194, 0.5);
            border: 1px solid transparent;
        }

        & span {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 44px;
            line-height: 110%;
            color: var(--cveta-zelenyy);
            position: relative;
            z-index: 2;
        }
        & p {
            font-weight: 400;
            line-height: 140%;
            color: var(--cveta-zelenyy);
            position: relative;
            z-index: 2;
        }
        &_title p {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 22px;
            line-height: 140%;
            color: #003649;
        }
        &_width p {
            max-width: 70%;
        }
    }

    &__btn-circle {
        width: 40px;
        min-width: 40px;
        height: 40px;
        background: var(--cveta-zelenyy);
        &::after {
            border-top: 1px solid var(--tekst-belyy);
            border-right: 1px solid var(--tekst-belyy);
        }
        &:hover {
            opacity: 0.8;
        }
    }

    &__img {
        position: absolute;
        bottom: 0;
        right: 20px;
    }
}


// media sub-stock-3
@media (max-width: 1100px) {
    .substock {
        &__row {
            flex-direction: column;
            gap: 10px;
        }
    }
}
@media (max-width: 680px) {
    .substock {
        &__between {
            align-items: center;
            gap: 14px;
        }
        &__item {
            border-radius: 20px;
            min-height: 188px;
            padding: 20px;
            &_title p {
                font-size: 16px;
            }
            & span {
                font-size: 26px;
            }
        }
        &__btn-circle {
            width: 35px;
            min-width: 35px;
            height: 35px;
        }
        &__img {
            right: 0;
            height: 95%;
        }
    }
}