
// page-reviews-1
.page-reviews {
    &__title {
        margin-bottom: 50px;
    }

    &__grid {
        display: grid;
        grid-template-columns: 4.2fr 8fr;
        gap: 60px;
    }

    &__select {
        border: 1px solid rgba(7, 57, 58, 0.2);
        border-radius: 30px;
        background-color: #fff;
        color: var(--cveta-zelenyy);
        opacity: 1;
    }

    &__sort {
        display: flex;
        flex-direction: column;
        gap: 36px;

        &_text {
            margin-bottom: 6px;
        }
        
        &_block {
            display: flex;
            flex-direction: column;
            gap: 12px;
        }

        &_services {
            display: flex;
            flex-direction: column;
            gap: 12px;
            max-height: 308px;
            overflow: hidden;
        }

        &_item {
            display: flex;
            gap: 14px;
            align-items: center;
            margin-bottom: 0;

            & input[type="checkbox"] {
                position: relative;
                width: 28px;
                height: 28px;
                color: #000;
                border: 1px solid  #556473;
                border-radius: 5px;
                appearance: none;
                outline: 0;
                cursor: pointer;
                transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
                &::before {
                    position: absolute;
                    content: '';
                    display: block;
                    top: 3px;
                    left: 9px;
                    width: 8px;
                    height: 14px;
                    border-style: solid;
                    border-color: #fff;
                    border-width: 0 3px 3px 0;
                    transform: rotate(45deg);
                    opacity: 0;
                }
                &:checked {
                    color: #fff;
                    border-color: var(--cveta-zelenyy-2);
                    background: var(--cveta-zelenyy-2);
                    &::before {
                        opacity: 1;
                    }
                }
            }
            & label {
                position: relative;
                font-weight: 500;
                line-height: 140%;
                color: var(--tekst-svetlyy);
                &::before {
                    position: absolute;
                    content: attr(data-content);
                }
            }
        }
    }

    &__quantity {
        font-size: 14px;
    }

    &__btn {
        width: 100%;
    }

    &__cards {
        display: flex;
        flex-direction: column;
        gap: 40px;
    }

    &__card {
        padding-top: 10px;
        border-top: 1px solid rgba(15, 86, 88, 0.2);

        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            margin-bottom: 30px;
        }

        &_labels {
            display: flex;
            gap: 10px;
            & p {
                border-radius: 40px;
                padding: 8px 16px;
                background: rgba(195, 241, 194, 0.4);
                font-weight: 500;
                font-size: 14px;
                color: var(--cveta-zelenyy);
            }
        }

        &_stars {
            display: flex;
            gap: 2px;
        }
        &_star-active {
            & svg path {
                fill:  var(--cveta-zelenyy-2);
            }
        }

        &_info {
            display: flex;
            gap: 30px;
            margin-bottom: 30px;
        }

        &_author {
            flex: 1 0 34%;
            display: flex;
            align-items: center;
            gap: 16px;
            padding-right: 30px;
            border-right: 1px solid #c7d6dc;
        }

        &_photo {
            display: block;
            border-radius: 35px;
            min-width: 52px;
            width: 52px;
            height: 52px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_fio {
            font-size: 14px;
            & p:first-child {
                font-family: var(--second-family);
                font-weight: 600;
                font-size: 18px;
                line-height: 133%;
                color: var(--cveta-zelenyy);
            }
        }

        &_service {
            flex: 1 0 60%;
            display: flex;
            flex-direction: column;
            gap: 8px;
            & span {
                font-weight: 500;
                color: var(--tekst-temnyy);
            }
        }

        &_main {
            margin-bottom: 20px;
        }

        // &_text {
        //     margin-bottom: 14px;
        //     max-height: 176px;
        //     overflow: hidden;
        // }

        // &_more::after {
        //     transform: rotate(135deg);
        //     margin-bottom: 2px;
        // }

        &_bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
        }

        &_publication {
            display: flex;
            align-items: center;
            gap: 10px;
            font-weight: 400;
            font-size: 14px;
        }

        &_source {
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            color: var(--fony-plashek-seryy-2);
        }

        &_answer {
            border: 1px solid var(--tekst-svetlyy);
            border-radius: 20px;
            padding: 20px;
            margin-top: 30px;
        }

        &_answered {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst-svetlyy);
            margin-top: 20px;
            & p:last-child {
                font-weight: 500;
                font-size: 16px;
            }
        }
    }
}

// media page-reviews-1
@media (max-width: 1000px) {
    .page-reviews {
        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 30px;
        }
        &__sort {
            display: contents;
        }
        &__btn {
            order: 3;
        }
    }
}
@media (max-width: 700px) {
    .page-reviews {
        &__title {
            margin-bottom: 30px;
        }    
        &__grid {
            gap: 20px;
        }
        &__sort {
            &_text {
                margin-bottom: 4px;
            }
            &_item {
                display: flex;
                gap: 14px;
                align-items: center;
                & input[type="checkbox"] {
                    width: 20px;
                    height: 20px;
                    &::before {
                        border-width: 0 2px 2px 0;
                        height: 12px;
                        width: 6px;
                        top: 2px;
                        left: 6px;
                    }
                }
                & label {
                    font-size: 14px;
                }
            }
        }
        &__quantity {
            padding: 10px 0;
        }
        &__card {
            padding-top: 30px;
            &:last-child {
                padding-bottom: 30px;
                border-bottom: 1px solid #c7d6dc;
                margin-bottom: 10px;
            }
    
            &_top {
                flex-direction: column;
                align-items: stretch;
                gap: 10px;
                margin-bottom: 0;
            }
            &_labels {
                margin-bottom: 20px;
                flex-wrap: wrap;
                & p {
                    padding: 6px 10px;
                }
            }
            &_stars {
                margin-left: 92px;
                margin-bottom: -24px;
            }
            &_info {
                flex-direction: column;
                gap: 16px;
                margin-bottom: 16px;
            }
            &_author {
                padding-right: 0;
                border-right: none;
                padding-bottom: 16px;
                border-bottom: 1px solid #c7d6dc;
            }
            &_photo {
                min-width: 76px;
                width: 76px;
                height: 76px;
            }
            &_fio {
                margin-top: 24px;
            }
            &_service {
                gap: 12px;
                & span {
                    display: block;
                }
            }
            &_main {
                margin-bottom: 16px;
            }
            // &_text {
            //     max-height: 216px;
            // }
            &_bottom {
                flex-direction: column;
                align-items: stretch;
                gap: 10px;
            }
            &_source {
                font-size: 16px;
            }
            &_answered {
                flex-direction: column;
                align-items: flex-start;
                gap: 4px;
                font-size: 14px;
                padding-top: 14px;
                border-top: 1px solid rgba(84, 84, 84, 0.2);
                margin-top: 14px;
            }
        }
    }
}