// text-block-1
.text-block {
    &__grid {
        display: grid;
        grid-template-columns: 4fr 8fr;
        gap: 60px;
        &:not(:last-child) {
            margin-bottom: 40px;
        }
    }

    &__left, &__right {
        padding-top: 20px;
        border-top: 1px solid rgba(111, 111, 111, 0.2);
    }

    & h2, & h3, & h4, & h5, & h6 {
        font-family: var(--second-family);
        font-weight: 600;
        line-height: 140%;
        color: var(--tekst-temnyy);
        margin-bottom: 20px;
    }
    & h2 {
        font-size: 34px;
    }
    & h3 {
        font-size: 30px;
    }
    & h4 {
        font-size: 26px;
    }
    & h5 {
        font-size: 18px;
    }
    & h2:empty, & h3:empty, & h4:empty, & h5:empty, & h6:empty {
        margin-bottom: 0;
    }

    & p {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    & span {
        font-weight: 500;
    }
    & a:not(.phone-btn-white) {
        font-weight: 500;
        text-decoration: underline;
        color: var(--fony-plashek-seryy-2);
        transition: all 0.3s ease;
        &:hover {
            color: var(--cveta-zelenyy);
        }
    }

    & ul:not(.breadcrumbs) {
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-bottom: 20px;
        & li {
            display: flex;
            gap: 10px;
            &::before {
                content: "";
                display: block;
                min-width: 10px;
                width: 10px;
                height: 10px;
                border-radius: 100%;
                background-color: var(--cveta-zelenyy-2);
                margin-top: 7px;
            }
        }
    }

    & ol {
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-bottom: 20px;
        list-style-type: none;
        counter-reset: item;
        & li {
            display: flex;
            gap: 10px;
            &::before {
                content: counter(item);
                counter-increment: item;
                display: block;
                min-width: 28px;
                width: 28px;
                height: 28px;
                border: 1px solid var(--fony-plashek-seryy-2);
                border-radius: 20px;
                font-weight: 500;
                font-size: 12px;
                color: var(--fony-plashek-seryy-2);
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }

    &__img {
        & img {
            border-radius: 10px;
            width: 100%;
            object-fit: cover;
            margin-bottom: 10px;
        }
        & p {
            font-weight: 400;
            font-size: 14px;
            color: var(--cveta-zelenyy);
            margin: 0;
        }
    }

    &__form {
        border-radius: 20px;
        padding: 40px 80px;
        background: var(--cveta-zelenyy);
        color: var(--tekst-belyy);
        overflow: hidden;
        display: flex;
        gap: 20px;
        margin: 80px 0;
        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 30px;
            line-height: 140%;
        }
    }

    & table {
        display: block;
        width: 100%;
        border: none;
        border-radius: 10px;
        overflow: hidden;
        border-collapse: collapse; 
        margin-bottom: 20px;

        & thead {
            & th {
                border: none;
                padding: 20px 25px;
                background: var(--cveta-salatovyy);
                font-weight: 500;
                font-size: 14px;
                color: var(--tekst-temnyy);
                border-right: 2px solid #fff;
            }
            & tr th:last-child {
                border-right: none;
            }
        }

        & tbody {
            & td {
                border-right: 2px solid #fff;
                padding: 20px;
                font-weight: 400;
                font-size: 14px;
                vertical-align: top;
            }
            & tr td:last-child {
                border-right: none;
            }
            & tr:nth-child(even){
                background: var(--fony-plashek-svetlo-seryy);
            }
        }
    }

    &__literature {
        &_title {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst);
            margin-bottom: 40px;
        }

        & ol {
            display: flex;
            flex-direction: column;
            gap: 10px;
            padding-bottom: 30px;
            border-bottom: 1px solid rgba(111, 111, 111, 0.2);
            margin-bottom: 30px;
            list-style-type: none;
            counter-reset: item;
            & li {
                display: flex;
                gap: 12px;
                color: var(--tegi);
                opacity: 0.85;
                &::before {
                    content: counter(item);
                    counter-increment: item;
                    display: block;
                    min-width: max-content;
                    border: none;
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 140%;
                    color: var(--cveta-zelenyy);
                    opacity: 0.4;
                }
            }
        }

        &_bottom {
            display: flex;
            gap: 20px;
            justify-content: space-between;
        }

        &_item {
            font-weight: 700;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            display: flex;
            gap: 15px;
            justify-content: space-between;
            & p {
                margin: 0 !important;
            }
        }

        &_share {
            font-weight: 500;
            font-size: 16px;
            color: var(--tekst-svetlyy);
        }

        &_links {
            display: flex;
            gap: 6px;
        }

        &_link {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 26px;
            height: 26px;
            border-radius: 5px;
            background: var(--cveta-zelenyy-2);
        }
    }


    &__left-info {
        margin-top: 40px;
        display: flex;
        flex-direction: column;
        gap: 40px;
    }
    &__left {
        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 24px;
        }
        &_services {
            border-radius: 20px;
            padding: 24px 28px;
            background: var(--fony-plashek-svetlo-seryy);
        }
        &_services-items {
            & a {
                font-weight: 500;
                text-decoration: none !important;
                color: var(--tekst-svetlyy);
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 16px 0;
                border-bottom: 1px solid rgba(111, 111, 111, 0.2);
                &::after {
                    border-top: 1px solid var(--cveta-zelenyy-2);
                    border-right: 1px solid var(--cveta-zelenyy-2);
                }
            }
        }

        &_list a:last-child {
            font-weight: 400;
            font-size: 16px;
            text-decoration: none;
            color: var(--cveta-zelenyy);
        }
        &_list-items {
            & a:nth-child(n) {
                font-weight: 400;
                text-decoration: none;
                color: var(--fony-plashek-seryy-2);
                display: flex;
                gap: 10px;
                margin-bottom: 16px;
                &::before {
                    content: "";
                    display: block;
                    width: 8px;
                    height: 8px;
                    border-radius: 100%;
                    background: var(--cveta-salatovyy);
                    margin-top: 7px;
                    transition: all 0.3s ease;
                }
            }
        }
    }

    &__widget {
        border-radius: 20px;
        padding: 24px 28px;
        background: var(--cveta-zelenyy) url("./../img/intro/intro-bg-3.jpg") center no-repeat;
        background-size: cover;
        position: relative;
        &_arrow {
            position: absolute;
            top: 20px;
            right: 20px;
        }
        &_row {
            display: flex;
            align-items: center;
            gap: 24px;
        }
        &_img {
            display: block;
            min-width: 75px;
            width: 75px;
            height: 75px;
            position: relative;
            & img {
                width: 100%;
                height: 100%;
                border-radius: 100%;
                object-fit: cover;
            }
            &::after {
                content: "";
                display: block;
                width: 22px;
                height: 22px;
                border-radius: 100%;
                background-color: #21ff1a;
                border: 3px solid var(--cveta-zelenyy);
                position: absolute;
                right: 0;
                bottom: 0;
            }
        }
        &_text {
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: var(--tekst-belyy);
            & span {
                display: block;
                font-weight: 400;
                font-size: 12px;
                line-height: 140%;
                color: rgba(255, 255, 255, 0.8);
                margin-bottom: 5px;
            }
        }
    }
}



// media text-block
@media (max-width: 1100px) {
    .text-block {
        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 0;
            padding-top: 20px;
            border-top: 1px solid #c7d6dc;
        }
        &__right {
            display: contents;
        }
        &__left {
            display: contents;
        }
        & h2, & h3, & h4, & h5, & h6 {
            order: -2;
        }
        &__img {
            order: 2;
            margin-bottom: 20px;
            & img {
                max-height: 400px;
            }
        }
        &__form {
            margin: 0 0 20px 0;
            padding: 40px;
        }
        &__left-info {
            display: none;
        }
    }
}
@media (max-width: 688px) {
    .text-block {
        &__grid {
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }
        & h2 {
            font-size: 24px;
        }
        & h3 {
            font-size: 22px;
        }
        & h4 {
            font-size: 20px;
        }
        & h5 {
            font-size: 16px;
        }
        & ul li {
            &::before {
                min-width: 8px;
                width: 8px;
                height: 8px;
            }
        }
        &__img {
            & img {
                max-height: 230px;
            }
            & p {
                font-size: 12px;
            }
        }
        &__form {
            border-radius: 10px;
            padding: 20px;
            flex-direction: column;
            gap: 0;
            &_title {
                font-size: 22px;
            }
        }
        & table {
            overflow-x: auto;
            max-width: 100%;
            & thead th, & tbody td {
                min-width: 243px;
            }
            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }
        &__literature {
            &_title {
                font-size: 20px;
                margin-bottom: 20px;
            }
            & ol {
                margin-bottom: 14px;
                & li {
                    font-size: 14px;
                    &::before {
                        font-size: 18px;
                    }
                }
            }
            &_bottom {
                flex-direction: column;
                gap: 14px;
            }
            &_item {
                padding-bottom: 14px;
                font-size: 14px;
                border-bottom: 1px solid #c7d6dc;
                align-items: center;
            }
            &_share {
                font-size: 14px;
            }
            &_links {
                display: flex;
                gap: 10px;
            }
            &_link {
                width: 32px;
                height: 32px;
            }
        }
    }
}