// doctor-page-1

.doctor-page {
    &__grid {
        display: grid;
        grid-template-columns: 3fr 6fr;
        gap: 40px;
        align-content: normal;
    }

    &__photo {
        width: 100%;
        height: 100%;
        max-width: 430px;
        max-height: 430px;
        position: relative;
        margin-bottom: 30px;
    }

    &__img {
        width: 100%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 30px;
        }
    }

    &__rating {
        position: absolute;
        bottom: 16px;
        left: 16px;
        right: 16px;
        border-radius: 30px;
        padding: 10px 16px;
        backdrop-filter: blur(8.100000381469727px);
        background: rgba(7, 57, 58, 0.6);
        display: flex;
        align-items: center;
        justify-content: center;

        &_estimation {
            padding-right: 20px;
            border-right: 1px solid rgba(255, 255, 255, 0.1);
            margin-right: 20px;
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            color: var(--tekst-belyy);
            display: flex;
            gap: 5px;
        }

        &_stars {
            display: flex;
            gap: 2px;
        }
        &_star {
            opacity: 0.6;
        }
    }

    &__prices {
        display: flex;
        gap: 10px;
        margin-bottom: 10px;

        & p {
            border: 1px solid var(--cveta-salatovyy);
            border-radius: 14px;
            padding: 14px 20px;
            font-size: 14px;
            text-align: center;
            flex: 1 0 48%;
            & span {
                font-weight: 700;
                font-size: 16px;
                color: var(--tekst-temnyy);
            }
        }
    }

    &__btn {
        width: 100%;
    }

    &__btns {
        margin-bottom: 40px;
    }

    &__schedule {
        border-radius: 30px;
        padding: 30px;
        background: var(--fony-plashek-svetlo-seryy);
        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 30px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 30px;
        }
        &_item {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            padding: 14px 0px;
            border-bottom: 1px solid #c7d6dc;
            &:last-child {
                border-bottom: none;
            }
        }
        &_time {
            font-weight: 500;
            color: var(--cveta-zelenyy);
        }
        &_weekend {
            font-weight: 500;
            color: var(--color-zelenyy);
            opacity: 0.6;
        }
    }

    &__right {
        border-radius: 30px;
        padding: 40px;
        background: var(--fony-plashek-svetlo-seryy);
        display: flex;
        flex-direction: column;
        gap: 60px;

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 20px;
        }
    }

    &__content {
        display: contents;
    }

    &__info {
        display: flex;
        flex-direction: column;
        gap: 30px;

        &_experience {
            border-radius: 30px;
            padding: 8px 14px;
            background: rgba(195, 241, 194, 0.3);
            font-weight: 500;
            font-size: 14px;
            color: rgba(7, 57, 58, 0.8);
            width: fit-content;
            margin-bottom: 10px;
        }

        &_fio {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 44px;
            line-height: 110%;
            color: var(--tekst-temnyy);
        }

        &_post {
            display: flex;
            gap: 30px;
        }
    }

    &__post {
        &_item {
            font-size: 14px;
            line-height: 140%;
            &:first-child {
                padding-right: 30px;
                border-right: 1px solid #c7d6dc;
            }
        }
        &_text {
            margin-bottom: 5px;
        }
        &_post {
            font-weight: 500;
            font-size: 16px;
            color: var(--tekst-svetlyy);
        }
    }

    &__list ul {
        display: flex;
        flex-direction: column;
        gap: 20px;
        & li {
            display: flex;
            gap: 12px;
            color: var(--tekst-svetlyy);
            &::before {
                content: "";
                display: block;
                border-radius: 10px;
                min-width: 18px;
                width: 18px;
                height: 3px;
                background: var(--cveta-zelenyy);
                margin-top: 9px;
            }

            & span {
                font-weight: 700;
                color: rgba(7, 57, 58, 0.6);
            }
        }
    }

    &__diplomas {
        &_swiper {
            max-width: 773px;
            margin-bottom: 20px;
            height: auto;
        }
        &_swiper-slide {
            border-radius: 20px;
            overflow: hidden;
            background: var(--tekst-belyy);
            & a {
                display: block;
                border-radius: 20px;
                overflow: hidden;
                & p {
                    padding: 20px;
                    width: 100%;
                    font-size: 14px;
                    color: var(--tekst-temnyy);
                    transition: all 0.8s ease;
                }
            }
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 20px;
            }
        }
        &_swiper-pagination {
            justify-content: center;
        }
        &_picture {
            display: block;
            border-radius: 10px;
            overflow: hidden;
        }
    }

    &__services {
        &_list ul {
            display: flex;
            gap: 10px;
            flex-wrap: wrap;
            max-height: 102px;
            overflow: hidden;
            & a {
                border-radius: 8px;
                padding: 12px 16px;
                background: var(--tekst-belyy);
                font-weight: 400;
                color: var(--fony-plashek-seryy-2);
            }
        }
        &_btn {
            margin-top: 20px;
        }
    }
}


// media doctor-page
@media (max-width: 1300px) {
    .doctor-page {
        &__grid {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
        &__photo {
            margin: 0 auto;
        }
        &__btns {
            margin-bottom: 0;
        }
        &__right, &__left {
            display: contents;
        }
        &__info {
            order: -1;
        }
        &__content {
            display: flex;
            flex-direction: column;
            gap: 60px;
        }
        &__info, &__content {
            border-radius: 20px;
            padding: 30px;
            background: var(--fony-plashek-svetlo-seryy);
        }
        &__diplomas {
            &_swiper {
                max-width: 100%;
            }
        }
    }
}
@media (max-width: 688px) {
    .doctor-page {
        &__photo {
            max-width: 350px;
        }
        &__rating {
            bottom: 10px;
            left: 10px;
            right: 10px;
            padding: 8px 16px;
            &_estimation {
                font-size: 18px;
            }
        }
        &__prices {
            & p {
                padding: 14px 20px;
                & span {
                    display: block;
                    font-size: 16px;
                }
            }
        }
        &__schedule {
            border-radius: 20px;
            padding: 20px;
            &_title {
                font-size: 20px;
                margin-bottom: 14px;
            }
        }
        &__right {
            &_title {
                font-size: 20px;
                margin-bottom: 20px;
            }
        }
        &__info, &__content {
            border-radius: 20px;
            padding: 20px;
        }
        &__content {
            gap: 30px;
        }
        &__info {
            gap: 20px;
            &_fio {
                font-size: 22px;
            }
            &_post {
                flex-direction: column;
                gap: 12px;
            }
        }
        &__post {
            &_item {
                font-size: 12px;
                &:first-child {
                    padding-right: 0;
                    border-right: none;
                    padding-bottom: 12px;
                    border-bottom: 1px solid #c7d6dc;
                }
            }
            &_post {
                font-size: 14px;
                color: var(--cveta-zelenyy);
            }
            
        }
        &__services {
            &_list ul {
                max-height: 206px;
            }
        }
    }
}

