
/* Общие glob */

html.lock {
    overflow: hidden;
}
html.burger-lock {
    overflow: hidden;
}
html.burger-lock::before {
    opacity: 1;
}
html::before {
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    //background-color: rgba(0, 0, 0, 0.3215686275);
    position: fixed;
    inset: 0;
    opacity: 0;
    z-index: 10;
    pointer-events: none;
    -webkit-transition: opacity 0.3s linear;
    -o-transition: opacity 0.3s linear;
    transition: opacity 0.3s linear;
}
.no-scroll {
    overflow: hidden;
}
body.lock {
    overflow: hidden;
}
body.burger-lock {
    overflow: hidden;
}

body {
    margin: 0;
    //overflow-x: hidden;
    overflow-y: auto;
    padding: 0;
    font-weight: 300;
    font-size: 16px;
    line-height: 140%;
    color: var(--fony-plashek-seryy-2);
    font-family: var(--font-family);
    background: var(--tekst-belyy);
}
.container {
	margin: 0 auto;
	padding: 0 var(--container-padding);
	max-width: var(--container-width);
	width: 100%;
}

.section-offset {
    margin-bottom: 160px;
}
.section-offset-mini {
    margin-bottom: 80px;
}

.section__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 50px;
}



/*  buttons */
.primary-btn, .secondary-btn, .tertiary-btn {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    color: var(--cveta-zelenyy);
    border-radius: 40px;
    padding: 16px 20px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    transition: all 0.3s ease;
    & span {
        position: relative;
        z-index: 2;
    }
    &:active {
        transform: scale(0.95);
    }
}

.primary-btn {
    background: var(--cveta-salatovyy);
    position: relative;
    overflow: hidden;
    &::after {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        background: #080b0f;
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
        transition: all 0.3s ease;
    }
}

.secondary-btn {
    background: var(--tekst-belyy);
}

.tertiary-btn {
    border: 1px solid var(--cveta-zelenyy);
    background: var(--tekst-belyy);
}

@media (hover: hover) {
    .primary-btn:hover::after {
        opacity: 0.2;
    }
    .secondary-btn:hover {
        opacity: 0.8;
    }
    .tertiary-btn:hover {
        background: var(--cveta-zelenyy);
        color: var(--tekst-belyy);
    }
}


.btn-arrow {
    display: block;
    font-weight: 500;
    font-size: 14px;
    line-height: 140%;
    text-align: left;
    color: var(--cveta-zelenyy);
    transition: all 0.3s ease;

    &::after {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 1px solid var(--cveta-zelenyy);
        border-right: 1px solid var(--cveta-zelenyy);
        transform: rotate(45deg);
        margin-left: 8px;
        transition: all 0.3s ease;
    }
    &:hover {
        opacity: 0.5;
    }
}
@media (hover: hover) {
    .btn-arrow {
        &:hover {
            opacity: 0.5;
            &::after {
                transform: rotate(45deg) translate(5px, -5px);
            }
        }
    }
}

.btn-circle {
    display: block;
    border-radius: 25px;
    width: 30px;
    min-width: 30px;
    height: 30px;
    background: var(--cveta-salatovyy);
    color: var(--cveta-zelenyy);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;

    &::after {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 1px solid var(--cveta-zelenyy);
        border-right: 1px solid var(--cveta-zelenyy);
        transform: rotate(45deg);
        margin-right: 4px;
        transition: all 0.3s ease;
    }
    &:hover {
        opacity: 0.5;
    }
}

.btn-more {
    &::after {
        transform: rotate(135deg);
        margin-bottom: 2px;
    }
}
@media (hover: hover) {
    .btn-more {
        &:hover {
            opacity: 0.5;
            &::after {
                transform: rotate(135deg) translate(2px, -2px);
            }
        }
    }
}
.btn-more.active {
    &::after {
        transform: rotate(315deg);
        margin-bottom: 0;
    }
}



/*  titles  */

.title-h1, .title-h2, .title-h3, .title-h4, .title-h5, .title-h6 {
    font-family: var(--second-family);
    font-weight: 600;
    line-height: 140%;
    color: var(--tekst-temnyy);
}
.title-h1 {
    font-size: 44px;
    line-height: 120%;
}
.title-h2 {
    font-size: 34px;
}
.title-h3 {
    font-size: 30px;
}
.title-h4 {
    font-size: 26px;
}
.title-h5 {
    font-size: 22px;
}
.title-h6 {
    font-size: 18px;
    line-height: 111%;
}

.phone {
    font-weight: 600;
    font-size: 26px;
    line-height: 140%;
    color: var(--cveta-salatovyy);
    display: flex;
    gap: 10px;
    align-items: center;
    transition: all 0.3s ease;
    &:hover {
        opacity: 0.5;
    }
}


.phone-btn,  .phone-btn-white {
    border-radius: 40px;
    padding: 10px 20px;
    font-family: var(--second-family);
    font-weight: 600;
    font-size: 22px;
    line-height: 140%;
    display: flex;
    gap: 10px;
    align-items: center;
    width: max-content;
    min-width: max-content;
    transition: all 0.3s ease;
    & svg path {
        transition: all 0.3s ease;
    }
}
.phone-btn {
    background: var(--cveta-zelenyy-2);
    color: var(--tekst-belyy);
    &:hover {
        background: var(--cveta-salatovyy);
    }
}
.phone-btn-white {
    background: var(--tekst-belyy);
    color: var(--cveta-zelenyy);
    &:hover {
        background: var(--cveta-zelenyy);
        color: var(--cveta-salatovyy);
        & svg path {
            fill: var(--cveta-salatovyy);
            stroke: var(--cveta-salatovyy);
        }
    }
}



/*  form  */

.input, .textarea {
    background: var(--tekst-belyy);
    border-radius: 30px;
    padding: 16px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: var(--tekst-temnyy);
    border: 1px solid transparent;
    transition: all 0.3s ease;
}
.input::placeholder, .textarea::placeholder {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #677281;
}
.input:hover, .textarea:hover {
    border: 1px solid var(--cveta-zelenyy-2);
}
.input:focus, .textarea:focus {
    border: 1px solid var(--cveta-zelenyy-2);
    background: var(--tekst-belyy);
}
.input:active, .textarea:active {
    border: 1px solid var(--cveta-zelenyy-2);
    background: var(--tekst-belyy);
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	appearance:none;
	-webkit-appearance:none;
}
.input-wrapper {
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    & .input {
        width: 100%;
    }
}
.input-clear {
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translate(0, -50%);
    z-index: 5;
    cursor: pointer;
}

.textarea {
    border-radius: 20px;
    width: 100%;
    min-height: 142px;
    resize: none;
}
.textarea:focus, .textarea:active {
    outline: none;
}

.politic {
    font-weight: 300;
    font-size: 14px;
    line-height: 140%;
    color: var(--fony-plashek-seryy-2);
    & a {
        color: inherit;
        font-weight: 500;
        text-decoration: underline;
        text-decoration-skip-ink: none;
        transition: all 0.3s ease;
        &:hover {
            opacity: 0.5;
        }
    }
}

/*  search  */
.form-search {
    position: relative;
    width: 100%;
    & input {
        // background: var(--fon-plashek);
        // opacity: 0.7;
        width: 100%;
        &:focus {
            opacity: 1;
        }
    }
    & img {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
    }
}

/* select */
select, .select {
    width: 100%;
    max-width: 100%;
    cursor: pointer;
    outline: none;
    border: 1px solid rgba(7, 57, 58, 0.2);
    border-radius: 30px;
    padding: 12px 24px;
    padding-top: 30px;
    padding-right: 30px;
    font-weight: 500;
    color: var(--cveta-zelenyy);
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    background: url(./../img/icons/arrow-bottom.svg) no-repeat right;
    background-position-x: calc(100% - 8px);
    transition: all 0.3s ease;

    &:hover {
        opacity: 1;
    }
    &:focus {
        outline: 1px solid var(--cveta-zelenyy-2);
    }
}


.sort_block {
    position: relative;
    & label {
        display: block;
        font-size: 12px;
        line-height: 140%;
        color: var(--fony-plashek-seryy-2);
        position: absolute;
        top: 10px;
        left: 24px;
        z-index: 2;
    }
}

.sort {
    &__text {
        color: var(--tekst);
        margin-bottom: 20px;
    }

    &__item {
        margin-bottom: 20px;
        display: flex;
        gap: 14px;
        align-items: center;

        & input[type="checkbox"] {
            position: relative;
            width: 28px;
            height: 28px;
            color: #000;
            border: 1px solid #788189;
            border-radius: 5px;
            appearance: none;
            outline: 0;
            cursor: pointer;
            transition: all 175ms cubic-bezier(0.1, 0.1, 0.25, 1);
            &::before {
                position: absolute;
                content: '';
                display: block;
                top: 3px;
                left: 9px;
                width: 8px;
                height: 14px;
                border-style: solid;
                border-color: #fff;
                border-width: 0 3px 3px 0;
                transform: rotate(45deg);
                opacity: 0;
            }
            &:checked {
                color: #fff;
                border-color: var(--color-zelenyy);
                background: var(--color-zelenyy);
                &::before {
                    opacity: 1;
                }
            }
        }
        & label {
            position: relative;
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst);
            &::before {
                position: absolute;
                content: attr(data-content);
            }
        }
    }
}

/* range */
input[type="range"] {
    -webkit-appearance: none; 
    appearance: none;
    width: 100%; 
    height: 4px; 
    background: rgb(16,153,110,0.2);
    border-radius: 20px;
    outline: none; 
    -webkit-transition: .2s;
    transition: opacity .2s;
    &::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        width: 16px;
        height: 16px;
        border-radius: 100%; 
        background: var(--cveta-zelenyy-2);
        cursor: pointer;
    }
    &::-moz-range-thumb {
        width: 16px;
        height: 16px;
        border-radius: 100%; 
        background: var(--cveta-zelenyy-2);
        cursor: pointer;
    }
    &:focus::-webkit-slider-thumb {
        outline: 2px solid var(--cveta-zelenyy-2);
        outline-offset: 0.125rem;
    }
    &:focus::-moz-range-thumb {
        outline: 2px solid var(--cveta-zelenyy-2);
        outline-offset: 0.125rem;
    }
    &:focus::-webkit-slider-thumb {
        outline: 2px solid var(--cveta-zelenyy-2);
        outline-offset: 0.125rem;
    }
}




/* accordion */
.accordion {
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    cursor: pointer;
    width: 100%;
    position: relative;

    &--active {
        .accordion__btn-more {
            transform: rotate(180deg);
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        -webkit-transition: all 0.4s ease;
        -o-transition: all 0.4s ease;
        transition: all 0.4s ease;
    }
}

/* tabs */
.tab__btn {
    transition: all 0.3s ease;
}
.tabcontent {
    transition: all 0.3s ease;
    display: none;
    overflow: hidden;
    &--active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}
.tab-content {
    display: none;
    &.active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}

/* rating */
.full-stars {
    text-align: center;
}
.full-stars .rating-group {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
}
.full-stars input {
    position: absolute;
    left: -9999px;
}
.full-stars label {
    margin: 0;
    cursor: pointer;
}
.full-stars label svg {
    margin: 0 2px;
    height: 18px;
    width: 20px;
    fill: var(--cveta-zelenyy-2);
    -webkit-transition: fill 0.3s;
    -o-transition: fill 0.3s;
    transition: fill 0.3s;
}
.full-stars input:checked ~ label svg {
    fill: transparent;
}
.full-stars .rating-group:hover label svg {
    fill: var(--cveta-zelenyy-2);
}
.full-stars .rating-group input:hover ~ label svg {
    fill: transparent;
}


/*  media global  */
@media (max-width: 800px) {
    .section-offset {
        margin-bottom: 80px;
    }
    .section {
        &__flex {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
        &__top {
            display: contents;
            margin-bottom: 20px;
        }
        &__btn {
            display: block;
            order: 3; 
            width: 100%; 
        }
        &__inner {
            order: 2;
        }
    }
}
@media (max-width: 688px) {
    body {
        font-size: 14px;
    }
    .title-h1 {
        font-size: 28px;
    }
    .title-h2 {
        font-size: 24px;
    }
    .title-h3 {
        font-size: 22px;
    }
    .title-h4 {
        font-size: 20px;
    }
    .title-h5 {
        font-size: 18px;
    }
    .title-h6 {
        font-size: 16px;
        line-height: 125%;
    }
    .btn-circle {
        width: 25px;
        min-width: 25px;
        height: 25px;
        &::after {
            width: 6px;
            height: 6px;
            margin-right: 1px;
        }
    }
    .phone {
        font-size: 18px;
    }
    .phone-btn,  .phone-btn-white {
        padding: 10px 16px;
        font-size: 18px;
    }

    .sort {
        &__text {
            margin-bottom: 14px;
        }
        &__item {
            margin-bottom: 14px;
            & input[type="checkbox"] {
                width: 22px;
                height: 22px;
                &::before {
                    top: 3px;
                    left: 7px;
                    width: 6px;
                    height: 10px;
                    border-width: 0 2px 2px 0;
                }
            }
            & label {
                font-size: 16px;
            }
        }
    }
}


