// gallery-page
.gallery-page__title-h1 {
    margin-bottom: 50px;
}

.gallery-page__tab {
    &_btns {
        display: flex;
        width: fit-content;
        margin-bottom: 50px;
        border-bottom: 1px solid rgba(107, 107, 107, 0.2);
    }
    
    &_btn {
        font-weight: 400;
        padding: 0px 40px 20px 40px;
        border-bottom: 2px solid transparent;
        transition: all 0.3s ease;
    
        &.tab__btn--active {
            border-bottom: 2px solid var(--cveta-zelenyy);
            color: var(--cveta-zelenyy);
        }
    }
}

.gallery-page__tabcontent {
    max-height: none !important;
}

.gallery-page__inner {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;

    &>* {
        flex-basis: calc(25% - 15px);
    }
}

.gallery-page__item {
    max-height: 225px;
    min-height: 225px;
    position: relative;
}

.zoom {
    position: relative;
}

.zoom::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(7, 57, 58, 0.5);
    border-radius: 10px;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 2;
}

.zoom::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60px;
    height: 60px;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    background: url("./../img/help/loupe.svg") no-repeat center center / cover;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 3;
}

.zoom:hover::before {
    opacity: 0.7;
}
.zoom:hover::after {
    opacity: 1;
}

.zoom:hover .gallery-page__item-descr {
    opacity: 0;
}
.gallery-page__item-picture {
    border-radius: 20px;
    position: relative;

    & img {
        border-radius: 20px;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.gallery-page__item-descr {
    position: absolute;
    border-radius: 20px;
    padding: 10px 16px;
    backdrop-filter: blur(29.600000381469727px);
    background: rgba(7, 57, 58, 0.6);
    font-size: 14px;
    line-height: 140%;
    color: var(--cveta-salatovyy);
    bottom: 4px;
    left: 4px;
    right: 4px;
    z-index: 3;
    height: auto;
    transition: opacity 0.3s ease;
}


// media gallery-page
@media (max-width: 1024px) {
    .gallery-page__inner {
        &>* {
            flex-basis: calc(33.333% - 13.33333px);
        }
    }
}
@media (max-width: 850px) {
    .gallery-page__inner {
        &>* {
            flex-basis: calc(50% - 10px);
        }
    }
}

@media (max-width: 550px) {
    .gallery-page__inner {
        gap: 10px;
        &>* {
            flex-basis: 100%;
        }
    }
    .gallery-page__title-h1 {
        margin-bottom: 30px;
    }
    .gallery-page__tab {
        &_btns {
            width: 100%;
            flex-wrap: wrap;
            gap: 15px 0;
            margin-bottom: 30px;
        }
        
        &_btn {
            flex: 1 0 auto;
            padding: 0px 16px 10px;
            border-bottom: 1px solid rgba(107, 107, 107, 0.2);
            &.tab__btn--active {
                border-bottom: 1px solid var(--cveta-zelenyy);
            }
        }
    }
}