// doctors-page-3
.doctors-page {
    &__title {
        margin-bottom: 50px;
    }

    &__card {
        display: flex;
        gap: 20px;
        color: var(--fony-plashek-seryy-2);
        &:not(:last-child){
            padding-bottom: 20px;
            border-bottom: 1px solid #c7d6dc;
            margin-bottom: 20px;
        }

        &_img {
            display: block;
            width: 100%;
            max-width: 224px;
            aspect-ratio: 1/1;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top;
                border-radius: 20px;
            }
        }

        &_experience {
            font-weight: 500;
            font-size: 14px;
            line-height: 140%;
            color: rgba(7, 57, 58, 0.8);
            border-radius: 30px;
            padding: 8px 14px;
            background: rgba(195, 241, 194, 0.3);
            margin-bottom: 8px;
            width: fit-content;
        }

        &_fio {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 30px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 8px;
        }

        &_top {
            margin-bottom: 20px;
        }

        &_prices {
            display: flex;
            gap: 10px;
            & p {
                border: 1px solid var(--cveta-salatovyy);
                border-radius: 14px;
                padding: 14px 20px;
                font-size: 14px;
                color: var(--fony-plashek-seryy-2);
                & span {
                    display: block;
                    font-size: 16px;
                    font-weight: 700;
                    color: var(--tekst-temnyy);
                }
            }
        }
    }
}



// media doctors-page-3
@media (max-width: 640px) {
    .doctors-page {
        &__title {
            margin-bottom: 40px;
        }
        &__card {
            display: flex;
            gap: 15px;
            flex-wrap: wrap;
            &:not(:last-child){
                padding-bottom: 25px;
                margin-bottom: 25px;
            }
            &_info {
                display: contents;
            }
            &_top {
                flex: 1 0 60%;
            }
            &_img {
                max-width: 90px;
                max-height: 114px;
                aspect-ratio: auto;
            }
            &_experience {
                padding: 6px 10px;
                font-size: 12px;
                margin-bottom: 10px;
            }
            &_top {
                margin-bottom: 0;
            }
            &_fio {
                font-size: 18px;
                margin-bottom: 6px;
            }
            &_post {
                width: 100%;
            }
            &_prices {
                width: 100%;
                & p {
                    flex: 1 0 45%;
                    padding: 14px 20px;
                }
            }
        }
    }
}

