// medicament-5
.medicament {
    &__title {
        margin-bottom: 50px;
    }
    
    &__row {
        display: flex;
        justify-content: space-between;
        gap: 60px;
    }

    &__content {
        flex: 1 0 45%;
    }

    &__list {
        max-height: 315px;
        overflow: hidden;
        transition: all 0.3s ease;
        margin-bottom: 20px;
        & ul {
            & li {
                display: flex;
                gap: 10px;
                margin-bottom: 16px;
                width: 100%;
                &:last-child {
                    margin-bottom: 0;
                }
                &::before {
                    content: "";
                    display: block;
                    width: 6px;
                    height: 6px;
                    border-radius: 100%;
                    background: var(--cveta-zelenyy-2);
                    margin-top: 8px;
                }
                & a {
                    display: block;
                    font-weight: 400;
                    line-height: 140%;
                    color: var(--tekst-temnyy);
                    width: 100%;
                    border-bottom: 1px solid rgba(111, 111, 111, 0.2);
                    padding-bottom: 16px;
                    transition: all 0.3s ease;
                    &:hover {
                        color:  var(--cveta-zelenyy);
                    }
                }
            }
        }
    }

    &__img {
        flex: 1 0 45%;
        & img {
            border-radius: 20px;
            width: 100%;
            height: 100%;
            max-height: 354px;
            object-fit: cover;
        }
    } 
}


// media medicament-5
@media (max-width: 800px) {
    .medicament {
        &__img {
            display: none;
        } 
    }
}
@media (max-width: 688px) {
    .medicament {
        &__title {
            margin-bottom: 30px;
        }
        &__list {
            max-height: 170px;
            margin-bottom: 12px;
            & ul {
                & li {
                    margin-bottom: 12px;
                    & a {
                        padding-bottom: 12px;
                    }
                }
            }
        }
    }
}