// medicament
.medicament {
    &__row {
        display: flex;
        gap: 60px;
    }

    &__title-h1 {
        margin-bottom: 40px;
    }

    &__left {
        flex: 1 0 28%;
    }

    &__info {
        flex: 1 0 65%;
        &_block:not(:last-child) {
            margin-bottom: 40px;
        }

        &_title {
            font-size: 26px;
            margin-bottom: 20px;
        }
    }

    &__img {
        display: block;
        border: 4px solid var(--fony-plashek-svetlo-seryy);
        border-radius: 20px;
        width: 100%;
        max-height: 350px;
        margin-bottom: 8px;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__warning {
        border-radius: 16px;
        padding: 16px 20px;
        background: var(--fony-plashek-svetlo-seryy);
        display: flex;
        gap: 10px;
        margin-bottom: 20px;
        &_sym {
            border: 1px solid var(--cveta-zelenyy-2);
            border-radius: 50px;
            font-weight: 600;
            color: var(--cveta-zelenyy-2);
            min-width: 24px;
            width: 24px;
            height: 24px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    &__author {
        padding: 16px;
        border-radius: 16px;
        background: var(--fony-plashek-seryy);
        display: flex;
        gap: 16px;

        &_photo {
            display: block;
            max-width: 107px;
            & img {
                width: 100%;
                height: 100%;
                border-radius: 6px;
                object-fit: cover;
            }
        }

        &_content {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 10px;
        }

        &_fio {
            font-weight: 700;
            color: var(--cveta-zelenyy);
        }
    }

}

// media medicament-2
@media (max-width: 1150px) {
    .medicament {
        &__row {
            flex-direction: column;
            gap: 30px;
        }
        
        &__title-h1 {
            order: -3;
            margin-bottom: 20px;
        }

        &__left {
            display: contents;
        }

        &__info {
            display: contents;
            &_block:first-child {
                display: contents;
            }
            &_block:not(:last-child) {
                margin-bottom: 10px;
            }
        }

        &__img {
            order: -2;
            margin-bottom: 0;
        }

        &__description {
            order: -1;
        }
    
        &__warning {
            margin-bottom: 0;
        }
    }
}
@media (max-width: 688px) {
    .medicament {
        &__title-h1 {
            margin-bottom: 0;
        }
        &__warning {
            border-radius: 12px;
        }
        &__author {
            border-radius: 12px;
            & p:first-child {
                font-size: 12px;
            }
            &_photo {
                max-width: 95px;
            }
        }
    }
}