// doctor-main

.doctor-main {
    &__grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }

    &__block {
        border-radius: 20px;
        padding: 40px;
        background: var(--fony-plashek-svetlo-seryy);

        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 140%;
            color: var(--fony-plashek-seryy-2);
            margin-bottom: 30px;
        }
    }

    &__info {
        grid-column: span 2;
        display: flex;
        gap: 40px;
        padding: 20px;
        background: var(--cveta-zelenyy);
        color: var(--tekst-belyy);

        &_photo {
            flex: 1 0 30%;
            display: block;
            width: 100%;
            max-width: 400px;
            aspect-ratio: 1/1;
            position: relative;
        }
    
        &_img {
            display: block;
            width: 100%;
            height: 100%;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 10px;
            }
        }

        &_experience {
            border-radius: 5px;
            padding: 14px 16px;
            background: var(--cveta-zelenyy);
            font-weight: 500;
            font-size: 14px;
            color: var(--tekst-belyy);
            text-align: center;
            position: absolute;
            left: 14px;
            right: 14px;
            bottom: 14px;
        }

        &_content {
            flex: 1 0 60%;
            padding: 30px 20px 30px 0;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 30px;
        }

        &_fio {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 44px;
            line-height: 110%;
            color: var(--tekst-belyy);
            padding-bottom: 16px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            margin-bottom: 16px;
        }

        &_post {
            display: flex;
            flex-direction: column;
            gap: 16px;
            padding-bottom: 16px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            margin-bottom: 16px;
            color: var(--tekst-belyy);
        }

        &_btns {
            display: flex;
            gap: 10px;
        }
    
        &_price {
            border: 1px solid var(--tekst-belyy);
            border-radius: 50px;
            padding: 15px 20px;
            font-size: 14px;
            line-height: 140%;
            text-align: center;
            & span {
                font-weight: 700;
                font-size: 16px;
                text-wrap: nowrap;
            }
        }

        &_bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
        }
    }

    &__post {
        &_item {
            font-size: 14px;
            line-height: 140%;
            display: flex;
            gap: 10px;
            & span {
                font-weight: 500;
            }
        }
        &_icon {
            display: block;
            min-width: 20px;
            width: 20px;
        }
    }

    &__rating {
        display: flex;
        align-items: center;
        gap: 10px;
        & p {
            padding-right: 10px;
            border-right: 1px solid rgba(111, 111, 111, 0.2);
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 22px;
            color: var(--tekst-belyy);
        }
    }

    &__schedule {
        &_item {
            color: var(--tekst-temnyy);
            display: flex;
            justify-content: space-between;
            gap: 16px;
            border-radius: 5px;
            padding: 14px 20px;
            &:nth-child(odd) {
                background: var(--tekst-belyy);
            }
        }
        &_time {
            font-weight: 500;
        }
        &_weekend {
            font-weight: 500;
            color: var(--cveta-zelenyy);
        }
    }

    &__tab {
        &_btns {
            display: flex;
            flex-wrap: wrap;
            gap: 15px 0;
            margin-bottom: 24px;
            border-bottom: 1px solid rgba(16, 153, 110, 0.2);
        }
        &_btn {
            flex-grow: 1;
            padding: 0px 20px 16px;
            border-bottom: 2px solid transparent;
            transition: all 0.3s ease;
            &:hover {
                color: var(--cveta-zelenyy-2);
            }
            &.active {
                border-bottom: 2px solid var(--cveta-zelenyy-2);
                color: var(--cveta-zelenyy-2);
            }
        }
    }

    &__list ul {
        display: flex;
        flex-direction: column;
        & li {
            display: flex;
            gap: 16px;
            font-weight: 400;
            color: var(--tekst-temnyy);
            position: relative;
            &:not(:last-child) {
                padding-bottom: 20px;
            }
            &::before {
                content: "";
                display: block;
                border-radius: 10px;
                min-width: 9px;
                width: 9px;
                height: 9px;
                background: var(--cveta-zelenyy);
                margin-top: 7px;
            }
            &::after {
                content: "";
                display: block;
                width: 1px;
                height: calc(100% - 28px);
                background: var(--cveta-zelenyy);
                position: absolute;
                top: 25px;
                left: 4px;
            }
            & span {
                font-weight: 700;
                color: var(--cveta-zelenyy);
            }
        }
    }

    &__diplomas {
        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            margin-bottom: 30px;
        }
        &_title {
            margin: 0;
        }
        &_swiper {
            max-width: 550px;
            margin-bottom: 20px;
            height: auto;
        }
        &_swiper-slide {
            border-radius: 10px;
            overflow: hidden;
            background: var(--belyy);
            & a {
                display: block;
                border-radius: 10px;
                overflow: hidden;
                & p {
                    padding: 16px;
                    width: 100%;
                    color: var(--tekst-temnyy);
                    text-align: center;
                }
            }
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 10px;
            }
        }
        &_swiper-pagination {
            justify-content: center;
        }
        &_picture {
            display: block;
            border-radius: 10px;
            overflow: hidden;
        }
    }

    &__works_list ul {
        display: flex;
        flex-direction: column;
        gap: 15px;
        & li {
            display: flex;
            gap: 12px;
            font-weight: 400;
            color: var(--tekst-temnyy);
            &::before {
                content: url("./../img/icons/doctor-main-bag.svg");
                display: block;
                min-width: 18px;
            }
        }
    }
}


// media doctor-main
@media (max-width: 1100px) {
    .doctor-main {
        &__grid {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
        &__info {
            display: contents;
            &_photo {
                margin: 0 auto;
            }
            &_content {
                border-radius: 20px;
                padding: 40px;
                background: var(--cveta-zelenyy);
                color: var(--tekst-belyy);
            }
        }
        &__diplomas {
            &_swiper {
                max-width: none;
            }
        }
    }
}
@media (max-width: 688px) {
    .doctor-main {
        &__block {
            border-radius: 10px;
            padding: 20px;
            &_title {
                font-size: 20px;
                margin-bottom: 20px;
            }
        }
        &__photo {
            max-width: 350px;
        }

        &__info {
            &_content {
                border-radius: 10px;
                padding: 20px;
            }
            &_fio {
                font-size: 20px;
            }
            &_bottom {
                flex-direction: column;
                align-items: stretch;
                gap: 20px;
            }
            &_btns {
                flex-direction: column-reverse;
                gap: 10px;
            }
        }
        &__post {
            &_item span {
                display: block;
            }
            &_icon {
                min-width: 16px;
                width: 16px;
            }
        }
        &__rating {
            justify-content: center;
        }
        &__diplomas {
            &_title {
                margin: 0;
            }
            &_top {
                margin-bottom: 20px;
            }
            &_swiper-btns {
                display: none;
            }
        }
        &__tab {
            &_btns {
                display: flex;
                flex-wrap: wrap;
                gap: 15px 0;
                margin-bottom: 24px;
                border-bottom: none;
            }
            &_btn {
                flex: 1 0 100%;
                padding: 0px 20px 16px;
                border-bottom: 1px solid rgba(16, 153, 110, 0.2);
                &.active {
                    border-bottom: 1px solid var(--cveta-zelenyy-2);
                    text-shadow: 0 0 1px var(--cveta-zelenyy-2);
                }
            }
        }
    }
}

