.popup-change {
    &__content {
        max-width: 1180px;
        border-radius: 30px;
        padding: 60px;
        box-shadow: 0 0 30px 0 rgba(255, 255, 255, 0.1);
        background: var(--tekst-belyy);
        display: flex;
        align-items: center;
    }

    &__left {
        padding-right: 40px;
        border-right: 1px solid var(--color-svetlo-zelenyy-2);
        margin-right: 40px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        flex-basis: 60%;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 600;
        font-size: 44px;
        line-height: 110%;
        color: var(--tekst-temnyy);
    }

    &__info {
        border: 1px solid rgba(7, 57, 58, 0.2);
        border-radius: 20px;
        padding: 20px;
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        gap: 26px;

        &_img {
            margin-bottom: -20px;
        }
    }

    &__phone-btn {
        font-size: 18px;
        color: var(--cveta-salatovyy);
        border-radius: 30px;
        padding: 6px 16px;
        background: var(--cveta-zelenyy);
        &:hover {
            color: var(--cveta-zelenyy);
            & svg path {
                fill: var(--cveta-zelenyy);
            }
        }
    }

    &__phone p {
        font-size: 14px;
        margin-bottom: 10px;
    }

    &__right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        flex-basis: 40%;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__input {
        border: 1px solid rgba(84, 84, 84, 0.2);
        background: rgba(255, 255, 255, 0.04);
        &:hover {
            background: rgba(255, 255, 255, 0.04);
        }
    }

    &__politic {
        color: var(--cveta-zelenyy);
    }
}



.popup-change-response {
    &__content {
        max-width: 583px;
        padding: 30px;
        position: relative;
        display: block;
    }
    &__top {
        position: absolute;
        top: -70px;
        left: 50%;
        transform: translate(-50%, 0);
        width: 140px;
        height: 140px;
        border-radius: 100px;
        box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.07);
        background-color: var(--belyy);
        padding: 25px;
        display: flex;
        align-items: center;
        justify-content: center;
        &::before {
            content: "";
            display: block;
            width: 50px;
            height: 35px;
        }
    }
    &__right {
        padding: 10px;
    }
    &__text {
        padding: 90px 30px 30px;
        text-align: center;
        & h3 {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 34px;
            line-height: 140%;
            color: var(--cveta-zelenyy);
            margin-bottom: 10px;
        }
    }

    &__btn {
        display: block;
        padding: 13px 20px;
        margin: 0 auto;
    }
}

.popup.popup-change-ok {
    & .popup-change-response__top {
        &::before {
            content: "";
            background-image: url("./../img/popups/popup-change-ok.svg");
            background-size: 100% 100%;
        }
    }
}
.popup.popup-change-error {
    & .popup-change-response__top {
        &::before {
            content: "";
            background-image: url("./../img/popups/popup-change-error.svg");
            background-size: 100% 100%;
        }
    }
}




// media popup-change-1
@media (max-width: 1000px) {
    .popup-change {
        &__content {
            border-radius: 20px;
            padding: 30px 20px 20px 20px;
            flex-direction: column;
            gap: 30px;
            max-width: 480px;
        }
        &__left {
            padding-right: 0;
            border-right: none;
            margin-right: 0;
            gap: 12px;
            flex-basis: 100%;
        }
        &__title {
            font-size: 22px;
        }
        &__info_img {
            display: none;
        }
        &__phone-btn {
            font-size: 16px;
        }
        &__phone p {
            font-size: 12px;
            margin-bottom: 14px;
        }
        &__right {
            gap: 20px;
        }
        &__close {
            top: 5px;
            right: 5px;
        }
    }

}
@media (max-width: 600px) {
    .popup-change-response {
        &__content {
            max-width: 430px;
            padding: 20px;
        }
        &__top {
            top: -40px;
            width: 80px;
            height: 80px;
            padding: 20px;
            &::before {
                width: 40px;
                height: 25px;
            }
        }
        &__right {
            padding: 10px;
        }
        &__text {
            padding: 46px 0px 20px;
            & h3 {
                font-size: 24px;
            }
        }
        &__btn {
            width: 100%;
        }
    }
}