// intro-article-2
.intro-article {
    position: relative;
    padding: 20px 0 40px;
    overflow: hidden;

    &__img-bg {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__row {
        display: flex;
        gap: 40px;
    }
    &__main {
        display: flex;
        flex-direction: column;
        gap: 40px;
        flex: 1 0 55%;
        & h1, h2 {
            color: var(--belyy);
            margin-bottom: 15px;
        }
        & p {
            color: var(--belyy);
        }
        &_marks {
            display: flex;
            gap: 20px;
        }
    }

    &__mark {
        border-radius: 30px;
        padding: 8px 10px;
        background: rgba(7, 57, 58, 0.6);
        display: flex;
        align-items: center;
        gap: 10px;
        & p {
            font-weight: 500;
            font-size: 14px;
            line-height: 140%;
            color: var(--cveta-salatovyy);
        }
        & span {
            font-weight: 300;
        }
    }

    &__right {
        flex-basis: 40%;
    }

    &__form {
        border-radius: 30px;
        padding: 30px;
        backdrop-filter: blur(12.100000381469727px);
        background: rgba(7, 57, 58, 0.6);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        height: 100%;
        position: relative;
        overflow: hidden;
        &_text, &_phone {
            max-width: 60%;
            position: relative;
            z-index: 2;
        }
        &_title {
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst-belyy);
            margin-bottom: 16px;
        }
        &_subtitle {
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst-belyy);
        }
        &_phone {
            background: var(--cveta-salatovyy);
            border-radius: 30px;
            padding: 8px 14px;
            font-weight: 600;
            font-size: 18px;
            line-height: 133%;
            color: var(--cveta-zelenyy);
        }
        &_img {
            position: absolute;
            bottom: 0;
            right: -20px;
            max-width: 60%;
            height: 100%;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

// media intro-article-2
@media (max-width: 1000px) {
    .intro-article {
        &__row {
            flex-direction: column;
            gap: 30px;
        }
    }
}
@media (max-width: 550px) {
    .intro-article {
        &__main {
            gap: 24px;
            &_bottom {
                flex-direction: column;
                gap: 10px;
            }
            &_marks {
                & span {
                    display: none;
                }
            }
        }
        &__form {
            border-radius: 20px;
            padding: 20px;
            &_text, &_phone {
                max-width: 90%;
            }
            &_title {
                font-size: 20px;
                margin-bottom: 16px;
            }
            &_img {
                right: -50px;
                bottom: -20px;
                max-height: 80%;
            }
        }
    }
}
