// features
.features {
    &__title {
        margin-bottom: 50px;
    }

    &__cards {
        display: flex;
        gap: 20px;
    }

    &__card {
        flex: 1 0 45%;
        padding: 40px;
        border-radius: 20px;
        background: var(--fony-plashek-svetlo-seryy);
        min-height: 100%;
        position: relative;
        overflow: hidden;
        & .features__card_btn {
            background-color: var(--tekst-belyy);
            width: 38px;
            height: 38px;
        }
        &:last-child {
            background: #eceff6;
            & .features__card_btn {
                background-color: var(--cveta-zelenyy);
                &::after {
                    border-top: 1px solid var(--tekst-belyy);
                    border-right: 1px solid var(--tekst-belyy);
                }
            }
        }

        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            font-family: var(--second-family);
            font-weight: 600;
            font-size: 30px;
            line-height: 140%;
            color: var(--cveta-zelenyy);
            margin-bottom: 24px;
            position: relative;
            z-index: 2;
        }

        &_list ul {
            display: flex;
            flex-direction: column;
            gap: 10px;
            position: relative;
            z-index: 2;
            & li {
                display: flex;
                gap: 10px;
                font-weight: 400;
                &::before {
                    content: "";
                    display: block;
                    width: 8px;
                    height: 8px;
                    border-radius: 100%;
                    background: var(--cveta-salatovyy);
                    margin-top: 7px;
                }
            }
        }

        &_bg {
            position: absolute;
            bottom: 0;
            right: 0;
        }
    }
}

// media features
@media (max-width: 800px) {
    .features {
        &__cards {
            flex-direction: column;
            gap: 16px;
        }
    }
}
@media (max-width: 688px) {
    .features {
        &__title {
            margin-bottom: 30px;
        } 
        &__card {
            padding: 20px 24px;
            border-radius: 10px;
            & .features__card_btn {
                display: none;
            }
            &_top {
                font-size: 22px;
                margin-bottom: 16px;
            }
            &_list ul {
                gap: 8px;
            }
    
            &_bg {
                position: absolute;
                bottom: 0;
                right: 0;
                width: 100%;
                //height: 100%;
                max-height: 90%;
                max-width: 55%;
                & img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }
    }
}